import $ from '../utils/api.request'

/**
 *
 
 @param { Number } gradeId 
 @param { Number } status   1- 2- 3-
 @param { Number } subjectId 
*/
export function getVoucherList (params) {
  return $.splatAxios.request({
    url: '/voucher/getVoucherList',
    method: 'post',
    data: params
  })
}

/**
 *
 
 http://apidoc.speiyou.cn/api/view/16465#
*/
export function queryInvoiceInfo (params, cityCode) {
  return $.splatAxios.request({
    url: '/voucher/queryInvoiceInfo',
    method: 'post',
    data: params,
    headers: {
      cityCode
    }
  })
}

