/**
 * 
 * @param str
 * @returns {boolean}
 */
export const validateInt = (str) => {
  const regExp = /^[0-9]*[1-9][0-9]*$/
  return regExp.test(str)
}

/**
 * 11
 * @param str
 * @returns {boolean}
 */
export const validatePhoneNumber = (str) => {
  const regExp = /^1[3456789]\d{9}$/
  return regExp.test(str)
}
