<script>
import { getUserExtInfoByType } from '../../api/bd'
import { queryInvoiceInfo, createInvoice } from '../../api/finance'
import { toChinesNum, getUserInfoByCookies, hasCnPt, hasCnPtwithoutBrkt } from '../../utils/utils'
import { queryServiceCenterByEmpNo } from '../../api/clazz'
import GroupMultiple from '../../utils/group'
import { querySubjectLimit } from '../../api/invoice'

export default {
  name: 'TalFinanceInvoiceTool',
  props: {
    mixinParams: { //  
      type: Array,
      required: true,
      default: () => {
        return []
      }
    },
    uid: {
      type: [String, Number],
      required: true
    },
    cityCode: {
      type: [String, Number],
      required: true
    },
    studentId: {
      type: String,
      required: true
    },
    studentNo: {
      type: String,
      required: true
    },
    studentName: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      invoiceData: {},
      tableData: [],
      loading: false,
      spanArr: [],
      groupData: [],
      total: 0, // 
      fixIndex: 0,
      fixedPrice: null,
      fixPriceModal: false,
      fixRefundModal: false,
      invoiceIndex: 0,
      invoiceFormModal: false,
      formData: {
        invoiceSubjectType: '0',
        buyerName: '',
        buyerIdentifyNum: '',
        buyerAddressPhone: '',
        buyerOpeningBank: '',
        receiverName: '',
        receiverEmail: '',
        receiverPhone: '',
        invoiceRemark: ''
      },
      ruleData: {
        buyerName: [
          { required: true, message: ', 50', trigger: 'blur', type: 'string' },
          {
            message: '()',
            trigger: 'blur',
            validator (...params) {
              return !hasCnPtwithoutBrkt(params[1])
            }
          }
        ],
        buyerIdentifyNum: [
          { pattern: /^[a-zA-Z0-9]{7,20}$/, type: 'string', trigger: 'change', message: '720' },
          {
            message: '(){}',
            trigger: 'blur',
            validator (...params) {
              return !hasCnPt(params[1])
            }
          }
        ],
        receiverName: { required: true, message: '', trigger: 'blur', type: 'string' },
        receiverEmail: [
          { required: true, message: '', trigger: 'blur', type: 'string' },
          { type: 'email', message: '', trigger: 'blur' }
        ],
        receiverPhone: [
          { pattern: /^\d{11}$/, message: '', trigger: 'blur' }
        ],
        buyerAddressPhone: {
          message: '(){}',
          trigger: 'blur',
          validator (...params) {
            return !hasCnPt(params[1])
          }
        },
        buyerOpeningBank: {
          message: '(){}',
          trigger: 'blur',
          validator (...params) {
            return !hasCnPt(params[1])
          }
        },
        invoiceRemark: {
          message: '(){}',
          trigger: 'blur',
          validator (...params) {
            return !hasCnPt(params[1])
          }
        }
      },
      confirmInvoiceModal: false,
      empInfo: null,
      scInfo: {},
      invoiceList: [],
      groupInvoice: [],
      queryInfo: [],
      tableLoading: []
    }
  },
  watch: {
    'formData.invoiceSubjectType' (val) {
      if (val === '0') {
        this.formData.buyerIdentifyNum = ''
        this.formData.buyerAddressPhone = ''
        this.formData.buyerOpeningBank = ''
      }
    }
  },
  mounted () {
    this.getEmail()
    this.computeParams()
    this.getData()
    this.empInfo = getUserInfoByCookies('columbusUserInfo')
    queryServiceCenterByEmpNo({
      cityCode: this.cityCode,
      empNo: this.empInfo.id
    }).then(data => {
      this.scInfo = data
    }).catch(err => {
      this.$Message.error(':' + err.message)
    })
  },
  methods: {
    getEmail () {
      getUserExtInfoByType({
        cityCode: this.cityCode,
        uid: this.uid,
        type: 'accessEmail'
      }).then(data => {
        this.formData.receiverEmail = data
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    },
    openFixModal (index, type = 0) {
      this.fixIndex = index
      if (type === 0) {
        this.fixPriceModal = true
      } else {
        this.fixRefundModal = true
      }
    },
    fix (type = 0) {
      if (type === 0) {
        if (this.fixedPrice !== null && /^\d+(\.\d{0,2})?$/.test(this.fixedPrice) && this.fixedPrice >= 0 && this.fixedPrice <= this.tableData[this.fixIndex].tempActualPrice) {
          this.tableData[this.fixIndex].actualPrice = this.fixedPrice
          this.fixPriceModal = false
          this.fixedPrice = null
        } else {
          this.$Message.warning({
            content: `0${this.tableData[this.fixIndex].tempActualPrice}`,
            duration: 5
          })
          return
        }
      } else {
        this.tableData[this.fixIndex].isRefund = this.tableData[this.fixIndex].isRefund ? 0 : 1
        this.fixRefundModal = false
      }
      console.log(this.tableData)
      this.computeGroupData()
    },
    cancelFix () {
      this.fixPriceModal = false
      this.fixedPrice = null
      this.fixRefundModal = false
    },
    toInvoice (index, item) {
      const parmas = {
        ledgerSubjectName: item.key.ledgerSubjectName,
        ledgerSubjectId: item.key.ledgerSubjectId,
        cityCode: this.cityCode
      }
      querySubjectLimit(parmas).then(limitPrice => {
        if (item.totalPrice > limitPrice) {
          this.$Modal.info({
            title: '',
            content: `${item.key.ledgerSubjectName}${limitPrice}`
          })
        } else {
          this.invoiceIndex = index
          this.invoiceFormModal = true
        }
      }).catch(err => {
        this.$Message.error({ content: ': ' + err.message, duration: 5 })
      })
    },
    confirmInvoice () {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.formData.invoiceSubjectType === '1' && !this.formData.buyerIdentifyNum) {
            this.$Message.warning('')
            return
          }

          this.invoiceFormModal = false
          this.confirmInvoiceModal = true
        }
      })
    },
    createInvoice () {
      const createInvoiceDetail = []
      this.groupData[this.invoiceIndex].value.forEach(g => {
        console.log(g)
        createInvoiceDetail.push({
          subOrderId: g.subOrderId,
          stagesNum: g.stagesNum,
          classNum: g.curriculumNum,
          classType: g.classType,
          haveClass: g.haveClass,
          amount: g.actualPrice,
          courseTime: g.startTime,
          belongClassId: g.classId,
          belongClassName: g.className,
          // classId: 'e394c6186546487380ba0c82ef8f0b6b',
          // className: 'B',
          uid: this.uid,
          studentNo: this.studentNo,
          studentId: this.studentId,
          studentName: this.studentName,
          stagesId: g.stagesId,
          stagesName: g.stagesName,
          rate: g.rate,
          areaCode: this.cityCode,
          ledgerSubjectName: g.ledgerSubjectName,
          ledgerSubjectId: g.ledgerSubjectId
        })
      })
      console.log(createInvoiceDetail)
      const params = Object.assign({}, {
        cityCode: this.cityCode,
        sellerName: this.groupData[this.invoiceIndex].key.ledgerSubjectName,
        operatorName: this.empInfo.name,
        operatorId: this.empInfo.id,
        serviceCenterName: this.scInfo.serviceCenterName,
        invoiceAmount: this.groupData[this.invoiceIndex].totalPrice,
        invoiceRate: this.groupData[this.invoiceIndex].value[0].rate,
        createInvoiceDetail
      }, this.formData)
      console.log(params)
      createInvoice(params).then(() => {
        this.$Message.success({
          content: '',
          duration: 5
        })
        this.confirmInvoiceModal = false
        this.getData()
        console.log('')
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    },
    confirmCancel () {
      this.confirmInvoiceModal = false
      this.invoiceFormModal = true
    },
    getData () {
      this.tableLoading = true
      queryInvoiceInfo({
        cityCode: this.cityCode,
        uid: this.uid,
        queryInfo: this.queryInfo
      }).then(data => {
        // data = { 'invoiceInfo': [{ 'registId': '48c22c112e18456caca8195a4c4261b4', 'stageInfo': [{ 'stagesNum': 1, 'stagesName': '', 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': 'LYN', 'classInfo': [{ 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '', 'curriculumNum': 3, 'shouldPrice': 167.0, 'actualPrice': 154.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': 'LYN', 'startTime': '2020-07-11 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '123456', 'rate': 0.03, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b43' }, { 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '', 'curriculumNum': 1, 'shouldPrice': 166.0, 'actualPrice': 153.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': 'LYN', 'startTime': '2020-05-16 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '12', 'rate': 0.02, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b41' }, { 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '', 'curriculumNum': 2, 'shouldPrice': 167.0, 'actualPrice': 153.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': 'LYN', 'startTime': '2020-06-13 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '123', 'rate': 0.02, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b42' }] }], 'subOrderId': '48c22c112e18456caca8195a4c4261b4' }, { 'registId': '48c22c112e18456caca8195a4c4261b3', 'stageInfo': [{ 'stagesNum': 1, 'stagesName': '', 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': 'LYN', 'classInfo': [{ 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '', 'curriculumNum': 3, 'shouldPrice': 167.0, 'actualPrice': 154.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': 'LYN', 'startTime': '2020-07-11 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '123456', 'rate': 0.03, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b43' }, { 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '', 'curriculumNum': 1, 'shouldPrice': 166.0, 'actualPrice': 153.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': 'LYN', 'startTime': '2020-05-16 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '123456', 'rate': 0.03, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b41' }, { 'registId': '48c22c112e18456caca8195a4c4261b4', 'stagesNum': 1, 'stagesName': '', 'curriculumNum': 2, 'shouldPrice': 167.0, 'actualPrice': 153.0, 'classId': '47f2665bfc73424ab01ebaad110af3f5', 'className': 'LYN', 'startTime': '2020-06-13 11:30:00', 'venueId': 'd0e203a8e63e47f0b439b8956169ee78', 'isRefund': 0, 'ledgerSubjectId': '194', 'ledgerSubjectName': '123456', 'rate': 0.03, 'hasInvoice': 2, 'invoiceCode': null, 'invoiceNum': null, 'invoiceTime': null, 'unionCurriculum': '48c22c112e18456caca8195a4c4261b42' }] }], 'subOrderId': '48c22c112e18456caca8195a4c4261b4' }], 'invoiceDetail': [], 'serviceTime': '2020-06-16 17:23:25' }
        this.invoiceData = data
        this.invoiceList = data.invoiceDetail
        this.computeTableData()
        this.computeGroupData()
        this.computeInvoice()
      }).catch(err => {
        console.error(err)
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    computeParams () {
      const data = this.mixinParams.map(rs => {
        const arr = rs.split('|')
        return {
          registId: arr[0],
          stagesNum: arr[1],
          venueId: arr[2],
          payTime: arr[3]
        }
      })
      const groupData = new GroupMultiple(data, ['registId', 'venueId']).group()
      const query = []
      groupData.forEach(g => {
        const stageInfo = g.value.map(v => {
          return {
            stagesNum: v.stagesNum,
            payTime: v.payTime
          }
        })
        query.push({
          registId: g.key.registId,
          venueId: g.key.venueId,
          stageInfo
        })
      })
      console.log(query)
      this.queryInfo = query
    },
    // 
    computeTableData () {
      this.tableData = this.invoiceData.invoiceInfo.map(info => {
        return info.stageInfo.map(s => s.classInfo.sort((s1, s2) => s1.curriculumNum - s2.curriculumNum).map(c => {
          c.classStageName = s.className + ' - ' + s.stagesName
          c.subOrderId = info.subOrderId
          return c
        })).reduce((l, r) => l.concat(r))
      }).reduce((l, r) => l.concat(r))
      // 
      let row = {}
      this.spanArr = []
      for (let i = 0; i < this.tableData.length; i++) {
        row = this.tableData[i]
        row.index = i + 1
        row.tempActualPrice = row.actualPrice // 
        row.rateText = row.rate === null ? '' : row.rate === 0 ? '' : row.rate * 100 + '%'
        row.hasInvoiceText = row.hasInvoice === 2
          ? '' : row.hasInvoice === 0
            ? '' : `${row.invoiceCode}${row.invoiceNum}${row.invoiceTime}`
        row.classTime = row.startTime.split(' ')[0]
        if (i === 0) {
          this.spanArr.push(1)
          this.pos = 0
        } else {
          // 
          if (row.classStageName === this.tableData[i - 1].classStageName) {
            this.spanArr[this.pos] += 1
            this.spanArr.push(0)
          } else {
            this.spanArr.push(1)
            this.pos = i
          }
        }
      }
      console.log(this.tableData)
    },
    /**
     * 
     */
    computeGroupData () {
      const data = this.tableData.filter(d => !d.isRefund && d.hasInvoice === 2 && d.ledgerSubjectName && d.rate !== null) // 
      console.log(data)
      this.groupData = new GroupMultiple(data, ['ledgerSubjectName', 'ledgerSubjectId', 'rateText', 'productName']).group()
      this.groupData.forEach(g => {
        g.totalPrice = g.value.reduce((l, r) => l + r.actualPrice, 0)
        g.indexs = g.value.reduce((l, r) => l + '' + r.index, '').slice(1)
      })
      this.groupData = this.groupData.filter(g => g.totalPrice > 0)
      this.total = this.groupData.reduce((l, r) => l + r.totalPrice, 0)
      console.log(this.groupData)
    },
    /**
     * 
     */
    computeInvoice () {
      this.groupInvoice = new GroupMultiple(this.invoiceList, ['invoiceStatus']).group().sort((a, b) => a.key.invoiceStatus - b.key.invoiceStatus)
      this.groupInvoice.forEach(g => {
        g.totalPrice = g.value.reduce((l, r) => l + r.invoiceAmount, 0)
      })
    },
    /**
     * 
     * @param num
     * @returns {string|string}
     */
    toCN (num) {
      return toChinesNum(num)
    }
  }
}
</script>

