<template>
    <div>
        <div style="padding: 15px">
            <Table :columns="tableColumns" :data="tableData" height="500"></Table>
        </div>
    </div>
</template>
<script>
import { clazzQueryCurriculumInfo, registPlayBack } from '../../api/clazz'
export default {
  name: 'TalSendPlaybackBysessions',
  props: {
    classInfo: {
      type: Object,
      default: () => {
        return {}
      }
    },
    creatorEmpNo: {
      type: String,
      default: ''
    },
    studentUid: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      tableColumns: [
        {
          title: '',
          key: 'classNum',
          align: 'center'
        },
        {
          title: '',
          align: 'center',
          minWidth: 40,
          render: (h, params) => {
            return h('div', [h('p', params.row.classDate)])
          }
        },
        {
          title: '',
          align: 'center',
          width: 40,
          render: (h, params) => {
            return h('div', [h('p', params.row.classDateWeek)])
          }
        },
        {
          title: '',
          key: 'classTimeName',
          align: 'center',
          minWidth: 40,
          render: (h, params) => {
            return h('span', params.row.classTimeName)
          }
        },
        {
          title: '',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '',
          key: 'teacherName',
          align: 'center'
        },
        {
          title: '',
          align: 'center',
          render: (h, params) => {
            return h(
              'span',
              {
                style: {
                  color:
                    params.row.cucClassType !== this.classInfo.classType
                      ? '#ed4014'
                      : ''
                }
              },
              this.getClassType(params.row.cucClassType)
            )
          }
        },
        {
          title: '',
          align: 'center',
          render: (h, params) => {
            return h('span', this.liveTypeMap[params.row.liveType])
          }
        },
        {
          title: '',
          key: 'classroomSimpleName',
          align: 'center'
        },
        {
          title: '',
          key: 'tutorSysName',
          align: 'center'
        },
        {
          title: '',
          key: 'classTeacherName',
          align: 'center'
        },
        {
          title: '',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h(
              'span',
              {
                style: {
                  color: params.row.status === 0 ? '#ed4014' : '#19be6b'
                }
              },
              params.row.status === 0 ? '' : ''
            )
          }
        },
        {
          title: '',
          key: 'caozuo',
          width: 80,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                style: {
                  display: params.row.status === 0 ? 'none' : 'inline-block'
                },
                props: {
                  type: 'primary',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.sendPlayback(params.row)
                  }
                }
              }, '')
            ])
          }
        }
      ],
      tableData: [],
      changeClassType: {
        1: '',
        2: '',
        4: '',
        5: ''
      },
      liveTypeMap: {
        0: '',
        1: '',
        2: '',
        3: '',
        4: '',
        5: ''
      }
    }
  },
  methods: {
    sendPlayback (row) {
      let params = {
        studentUid: this.studentUid,
        curriculumId: row.curriculumId,
        creator: this.creatorEmpNo,
        areaCode: this.classInfo.cityId,
        operationType: 14,
        registType: 4
      }
      registPlayBack(params).then(() => {
        this.$Message.success('')
        this.$emit('success')
      }).catch(error => {
        this.$Message.error(': ' + error)
      })
    },
    getClassType (val) {
      return this.changeClassType[val]
    },
    getTable () {
      const params = {
        areaCode: this.classInfo.cityId,
        classId: this.classInfo.classId ? this.classInfo.classId : this.classInfo.id ? this.classInfo.id : '',
        pageSize: 100
      }
      clazzQueryCurriculumInfo(params).then(res => {
        this.tableData = res.rows || []
      }).catch(error => {
        this.$Message.error(': ' + error)
      })
    }
  },
  created () {
    this.getTable()
  }
}
</script>
