<template>
  <div class="pay-details">
    <div class="row">
      <h3></h3>
      <section class="legends">
        <Tag color="#000" type="border" class="legend"></Tag>
        <Tag color="#999" class="legend"></Tag>
        <Tag color="#2d8cf0" class="legend"></Tag>
      </section>
    </div>
    <Row type="flex" justify="space-between" class="price-infos">
      <Spin size="large" fix v-if="classCountLoading"></Spin>
      <Col span="2" class="lineHeight30">
        <aside></aside>
        <aside></aside>
        <aside></aside>
      </Col>
      <template v-if="classTimes.length">
        <Col span="20">
          <ul class="price-info-list" :class="{'pd17': classTimes.length > 16}">
            <li v-for="item in classTimes" :key="item.curriculumNum">
              <aside>
                <span class="circle" :class="{dis: item.curriculumStatus === 1, can: item.curriculumStatus === 2}">{{item.curriculumNum}}</span>
              </aside>
              <aside :title="112999">{{fixedPrice(item.shouldPrice)}}</aside>
              <aside>{{fixedPrice(item.actualPrice)}}</aside>
            </li>
          </ul>
        </Col>
        <Col span="2" class="lineHeight30 txt-right">
          <aside></aside>
          <aside>{{totalShouldPrice}}</aside>
          <aside>{{totalActualPrice}}</aside>
        </Col>
      </template>
      <Col span="22" v-else >
        <p class="no-data"></p>
      </Col>
    </Row>
    <div class="row">
      <h3></h3>
    </div>
    <div class="history-table">
      <TalTable
        :tableData="tableData"
        :columns="columns"
        :loading="tableLoading"
      ></TalTable>
    </div>
    <div class="row">
      <h3>:</h3>
      <ol class="desc">
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ol>
    </div>
  </div>
</template>

<script>
import { queryClassPayments, queryCurriculumPayments } from '../../api/order'
import TalTable from '../tal-table'
export default {
  name: 'talFinancePaymentDetails',
  components: {
    TalTable
  },
  props: {
    cityCode: {
      required: true,
      type: String
    },
    registId: {
      type: String,
      required: true
    },
    uid: {
      type: String,
      required: true
    }
  },
  computed: {
  },
  data () {
    return {
      classTimes: [],
      tableData: [],
      columns: [
        {
          title: '',
          width: 50,
          align: 'center',
          slot: 'payType'
        },
        {
          title: '',
          align: 'center',
          key: 'className',
          width: 150
        },
        {
          title: '',
          align: 'center',
          key: 'classTypeName',
          width: 30
        },
        {
          title: '',
          align: 'center',
          slot: 'subjectName',
          width: 60
        },
        {
          title: '',
          align: 'center',
          slot: 'curriculumNum',
          width: 100
        },
        {
          title: '/',
          align: 'center',
          width: 100,
          slot: 'classInstallment'
        },
        {
          title: '',
          align: 'center',
          width: 100,
          slot: 'discountAmount'
        },
        {
          title: '',
          align: 'center',
          slot: 'price',
          width: 55
        },
        {
          title: '/',
          align: 'center',
          slot: 'availableClass',
          width: 150,
          renderHeader: (h, params) => {
            return h('span', [
              h('span', '/'),
              h('Tooltip', {
                props: {
                  content: '/',
                  'max-width': 200,
                  transfer: true,
                  placement: 'top'
                }
              }, [h('Icon', {
                style: {
                  marginTop: '-2px'
                },
                props: {
                  type: 'md-help-circle',
                  size: 16
                }
              })])
            ])
          }
        },
        {
          title: '',
          align: 'center',
          slot: 'dtsbDescription',
          width: 60
        },
        {
          title: '',
          align: 'center',
          key: 'createDate',
          width: 75
        },
        {
          title: '',
          align: 'center',
          slot: 'payTypeName',
          width: 55
        },
        {
          title: '',
          width: 200,
          align: 'center',
          key: 'orderNo'
        }
      ],
      classCountLoading: true,
      tableLoading: true,
      totalShouldPrice: 0,
      totalActualPrice: 0
    }
  },
  mounted () {
    this.initData()
  },
  methods: {
    initData () {
      let req = {
        registId: this.registId,
        uid: this.uid,
        empNo: '076757',
        cityCode: this.cityCode
      }
      this.queryClassPaymentsHandler(req)
      this.queryCurriculumPaymentsHandler(req)
    },
    queryClassPaymentsHandler (req) {
      queryClassPayments(req).then(rlt => {
        console.log(rlt)
        this.tableData = rlt
      }).catch(err => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    queryCurriculumPaymentsHandler (req) {
      queryCurriculumPayments(req).then(rlt => {
        this.classTimes = rlt
        this.totalActualPrice = this.sumPrice(rlt, 'actualPrice')
        this.totalShouldPrice = this.sumPrice(rlt, 'shouldPrice')
      }).catch(err => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
      }).finally(() => {
        this.classCountLoading = false
      })
    },
    sumPrice (data, attr) {
      let sum = 0
      data.forEach(obj => {
        sum += Number(obj[attr])
      })
      return sum.toFixed(2)
    },
    /**
      
     @params val 
     @params max   9999 99999 --
    */
    fixedPrice (val, max = 9999) {
      let _p = Number(val)
      if (_p) {
        return _p > max ? '--' : _p
      }
      return 0
    }
  }
}
</script>

