<template>
  <!---->
  <div>
    <div class="reset_top" style="padding: 15px;">
      <p></p>
      <Row>
        <Col span="4">
          <span>:</span>
          <span class="stuRight">{{studentDetail.realName}}</span>
        </Col>
        <Col span="6">
          <span>:</span>
          <span class="stuRight">{{studentDetail.card}}</span>
        </Col>
        <Col span="2">
          <span>:</span>
          <span class="stuRight">{{studentDetail.sex === 1 ? '': ''}}</span>
        </Col>
        <Col span="5">
          <span>:</span>
          <span class="stuRight">{{studentDetail.gradeName}}</span>
        </Col>
        <Col span="7">
          <span>:</span>
          <span class="stuRight">{{studentDetail.identifyCardNo || '--'}}</span>
        </Col>
      </Row>
      <Row v-if="studentDetail.attentionReqList && studentDetail.attentionReqList[0]">
        <Col span="4">
          <span>:</span>
          <span class="stuRight">{{studentDetail.attentionReqList[0].typeName}}</span>
        </Col>
        <Col span="6">
          <span>:</span>
          <span class="stuRight">{{studentDetail.attentionReqList[0].tel}}</span>
        </Col>
      </Row>
      <Row v-if="studentDetail.attentionReqList && studentDetail.attentionReqList[1]">
        <Col span="4">
          <span>:</span>
          <span class="stuRight">{{studentDetail.attentionReqList[1].typeName}}</span>
        </Col>
        <Col span="6">
          <span>:</span>
          <span class="stuRight">{{studentDetail.attentionReqList[1].tel}}</span>
        </Col>
      </Row>
      <Row style="border-bottom: 1px solid gainsboro;"></Row>
      <Form :label-width="100"
            :rules="ruleInline" label-position="left" style="margin-top: 20px;padding: 10px 10px 10px 10px">
        <FormItem label="">
          <Input v-model="newPassword" :type="'password'" style="width: 200px;margin-right: 10px"/>
          <Tooltip max-width="300" content="appIPS" placement="right">
            <Icon type="ios-alert-outline" size="18"/>
          </Tooltip>
        </FormItem>
        <FormItem label="">
          <Input v-model="confirmPassword" :type="'password'" style="width: 200px;"/>
        </FormItem>
        <FormItem label="">
          <Select v-model="newPhone" placeholder="" style="width: 200px;">
            <Option v-for="item in phoneList" :value="item.num" :key="item.num" >
              <template v-if="item.num === '0'"></template>
              <template v-else>{{ item.num }}</template>
                {{ item.phone }}
            </Option>
          </Select>
          <Button type="default" @click="sendMessageHandler(newPhone, 'bindCode')" v-if="!sendBtn"
            class="wd80" style="margin-left: 20px;">
            
          </Button>
          <Button type="default" :disabled="true" v-else class="wd80" style="margin-left: 20px;">{{msgNum}}</Button>
        </FormItem>
        <FormItem label=":">
          <Input v-model="smsCode" style="width: 150px"/>
        </FormItem>
      </Form>
    </div>
    <div class='ivu-modal-footer'>
      <Button type="primary" @click="updatePass()" :loading="isLoading"></Button>
      <Button @click="modalValue = false"></Button>
    </div>
  </div>
</template>
<script>
import { resetPassword } from '../../api/student'
import { queryPhones, sendMessage } from '../../api/sms'

export default {
  name: 'TalStuResetPwd',
  props: {
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      newPassword: '', // 
      confirmPassword: '', // 
      ruleInline: {
        newPassword: [
          { required: true, message: '', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      isLoading: false,
      sendBtn: false, // 
      msgNum: '', // 
      smsCode: '', // 
      newPhone: '',
      phoneList: []
    }
  },
  mounted () {
    let req = {
      cityCode: this.studentDetail.cityCode,
      uid: this.studentDetail.uid
    }
    queryPhones(req).then(res => {
      if (res) {
        this.phoneList = res.map(r => {
          r.num = String(r.num)
          return r
        })
      }
    }).catch(err => {
      this.$Message.warning({ content: ':' + err.message, duration: 3 })
    })
  },
  methods: {
    updatePass () {
      if (this.isLoading) {
        return
      }
      if (!this.newPassword) {
        this.$Message.warning('')
        return
      }
      if (!this.confirmPassword) {
        this.$Message.warning('')
        return
      }
      if (this.newPassword !== this.confirmPassword) {
        this.$Message.warning('')
        return
      }
      let req = {
        cityCode: this.studentDetail.cityCode,
        studentId: this.studentDetail.studentId,
        uid: this.studentDetail.uid,
        newPassword: this.newPassword,
        newConfirmedPassword: this.confirmPassword,
        smsCode: this.smsCode
      }
      this.isLoading = true
      resetPassword(req).then(res => {
        this.$Message.success('')
        this.isLoading = false
        this.modalValue = false
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
        this.isLoading = false
        // this.modalValue = false
      })
    },
    // sms
    sendMessageHandler () {
      if (!this.sendBtn) {
        if (!this.newPhone) {
          this.$Message.warning({
            content: '',
            duration: 3
          })
          return
        }
        this.sendBtn = true
        this.msgNum = 600
        let set
        let req = {
          num: this.newPhone,
          uid: this.studentDetail.uid,
          cityCode: this.studentDetail.cityCode,
          validType: 'resetPassword'
        }
        sendMessage(req).then(res => {
          this.$Message.success({ content: '', duration: 3 })
          set = setInterval(() => {
            if (this.msgNum <= 0) {
              clearInterval(set)
              this.sendBtn = false
            } else {
              this.msgNum -= 1
            }
          }, 1000)
        }).catch(err => {
          clearInterval(set)
          this.sendBtn = false
          this.$Message.error({
            content: err.message,
            duration: 5
          })
        })
      }
    }
  },
  computed: {
    modalValue: {
      get: function () {
        return this.value
      },
      set: function (val) {
        this.newPassword = ''
        this.confirmPassword = ''
        this.$emit('close', val)
      }
    }
  }
}
</script>
