<template>
  <div>
    <div @keyup.enter="search">
      <div style="padding: 0 5px 5px 5px;">
        <Row>
          <Col span="10">
            <span>:</span>
            <span class="stuRight">{{oldClassInfo.year + '+' + oldClassInfo.termName + '+' + oldClassInfo.gradeName + '+' + oldClassInfo.subjectNames + '+' + oldClassInfo.levelName + '+' + classTypeMap[oldClassInfo.classType]}}</span>
          </Col>
          <Col span="7">
            <span> / :</span>
            <span class="stuRight ivu-info-color">{{oldClassInfo.passedCount}} / {{oldClassInfo.classCount}}</span>
          </Col>
          <Col span="7">
            <span>:</span>
            <span class="stuRight">{{oldClassInfo.price}}</span>
          </Col>
        </Row>
        <Row style="margin-top: 10px">
          <Col span="10">
            <span>:</span>
            <span class="stuRight ivu-info-color">{{oldClassInfo.teacherNames}}</span>
          </Col>
          <Col span="7">
            <span>:</span>
            <span class="stuRight">{{oldClassInfo.tutorRealName}}</span>
          </Col>
          <Col span="7">
            <span>:</span>
            <span class="stuRight ivu-info-color">{{oldClassInfo.classroomName}}</span>
          </Col>
        </Row>
        <Row style="margin-top: 10px">
          <Col span="10">
            <span>:</span>
            <span class="stuRight">{{oldClassInfo.classtimeNames}}</span>
          </Col>
          <Col span="7">
            <span>:</span>
            <span class="stuRight">{{oldClassInfo.startDate}}</span>
          </Col>
          <Col span="7">
            <span>:</span>
            <span class="stuRight">{{oldClassInfo.endDate}}</span>
          </Col>
        </Row>
      </div>
      <div style="position: relative">
        <Form @submit.native.prevent style="margin-top: 10px" ref="form" :model="formData" inline>
          <FormItem>
            <Select v-model="formData.gradeCustomId" @on-change="gradeChange" placeholder="" style="width:120px">
              <Option v-for="item in gradeList" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select placeholder="" v-model="formData.bookVersion" clearable filterable transfer style="width: 120px;">
              <Option v-for="option in levelList" :value="option.id" :key="option.id">{{option.name}}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.categoryType" placeholder="" transfer style="width:120px">
              <Option v-for="item in classTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.maxPersonBoundary" placeholder="" transfer style="width:120px" clearable>
              <Option v-for="item in limitTags" :value="item.id" :key="item.value">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem prop="teacherName">
            <Select placeholder="" v-model="formData.teacherEmpNo" filterable clearable remote transfer
              @on-change="optionChange" :remote-method="getTeacherList" label-in-value style="width: 120px;" :loading="searchLoading">
              <Option v-for="option in teacherList" :value="option.empNo" :key="option.cityId">{{option.sysName}}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select placeholder="" v-model="formData.tutorEmpNo" clearable filterable remote transfer
              @on-change="optionChange" :remote-method="getTeacherList2" label-in-value style="width: 120px;" :loading="searchLoading">
              <Option v-for="option in tutorTeacherList" :value="option.empNo" :key="option.cityId">{{option.sysName}}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Input :value="oldClassInfo.subjectNames" disabled placeholder="" style="width:120px"/>
          </FormItem>
          <FormItem>
            <Select v-model="formData.classroomAreaId" @on-change="areaChange" placeholder="" filterable transfer clearable style="width:120px">
              <Option v-for="item in areaList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.classroomServiceCenterId" @on-change="serviceChange" filterable placeholder="" remote :remote-method="getServiceCenterList" transfer clearable style="width:120px">
              <Option v-for="item in serviceCenterList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.classroomVenueId" transfer @on-change="changeVenue" filterable placeholder="" remote :remote-method="getVenueList" clearable style="width:120px">
              <Option v-for="item in venueList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.classTimeTypeId" clearable placeholder=""  style="width:120px">
              <Option v-for="(item,index) in classTimeTypeList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.period" placeholder=""  clearable style="width:120px">
              <Option value=''></Option>
              <Option value=''></Option>
              <Option value=''></Option>
              <Option value=''></Option>
            </Select>
          </FormItem>
          <FormItem>
            <DatePicker placeholder="" :editable="false" v-model="startDate" @on-change="startDateChange" type="date" style="width: 114px">
            </DatePicker>
            <span style="margin:0 5px;"></span>
            <DatePicker placeholder="" :editable="false" v-model="endDate" @on-change="endDateChange" type="date" style="width: 113px">
            </DatePicker>
          </FormItem>
          <FormItem>
            <Select v-model="formData.isSchoolTest" placeholder="" clearable style="width:120px">
              <Option :value=1></Option>
              <Option :value=0></Option>
            </Select>
          </FormItem>
          <FormItem>
            <InputNumber :precision="0" :min="-1000" :max="9999" v-model="formData.surplusPersonsStart" placeholder="" style="width: 114px;"/>
            <span style="margin:0 5px;"></span>
            <InputNumber :precision="0" :min="1" :max="10000" v-model="formData.surplusPersonsEnd" placeholder="" style="width: 113px;"/>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="search"></Button>
          </FormItem>
        </Form>
      </div>
    </div>
    <div class="class_table">
      <Table :columns="tableColumns" :data="tableData"></Table>
      <div style="text-align: right">
        <Page
          :total="pageTotal"
          :page-size="pageSize"
          :current="pageNum"
          :page-size-opts="[5, 10, 20, 40]"
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"
          show-sizer
          show-total
          style="margin-top: 10px;"/>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getLinkage,
  getAreaList,
  getServiceCenterList,
  getAllServiceCenterList,
  getVenueList,
  getTeacherByNameFuzzy,
  getTimeTypeList
} from '../../api/bd'
import { queryTargetChangeClassList, queryClazzDic } from '../../api/clazz'
import Cookie from 'js-cookie'

export default {
  name: 'TalSearchChangeClassTarget',
  props: {
    // 
    oldClassInfo: {
      type: Object,
      default: () => { }
    },
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    },
    clientType: {
      type: Number,
      required: true,
      default: 3 // 3 17 
    }
  },
  data () {
    return {
      // 
      startDate: '',
      endDate: '',
      formData: {
        subjectCustomId: '', // id
        gradeCustomId: '', // id
        bookVersion: '', // 
        categoryType: '', // 
        teacherEmpNo: null, // 
        tutorEmpNo: null, // 
        classroomAreaId: '', // id
        classroomServiceCenterId: '', // id
        classroomVenueId: '', // id
        classTimeTypeId: '', // 
        isSchoolTest: '', // 
        surplusPersonsStart: null, // 
        surplusPersonsEnd: null, // 
        startDateFrom: '', // 
        startDateTo: '', // 
        maxPersonBoundary: '' // 
      },
      serviceCenterList: [], // 
      gradeList: [], // 
      levelList: [], // 
      classTypeList: [
        { value: '1', label: '' },
        { value: '2', label: '' },
        { value: '4', label: '' },
        { value: '16', label: '' }
      ], // 
      teacherList: [], // 
      tutorTeacherList: [], // 
      areaList: [], // 
      venueList: [], // 
      classTimeTypeList: [], // 
      // 
      classTypeMap: {
        1: '',
        2: '',
        4: '',
        16: ''
      },
      limitTags: [], // 
      // 
      tableData: [],
      pageTotal: 0, // 
      pageSize: 10, // 
      pageNum: 1, // 
      searchLoading: false,
      tableColumns: [
        {
          title: '',
          key: 'className',
          minWidth: 130,
          align: 'center',
          render: (h, params) => {
            return h(
              'a',
              {
                props: {
                  href: 'javascript:void(0)'
                },
                on: {
                  click: () => {
                    this.$emit('showClassDetail', params.row.classId)
                  }
                }
              },
              params.row.className
            )
          }
        },
        {
          title: '',
          key: 'categoryType',
          align: 'center',
          minWidth: 40,
          render: (h, params) => {
            return h(
              'span',
              {
                style: {}
              },
              this.getClassType(params.row.categoryType)
            )
          }
        },
        { title: '', key: 'subjectName', align: 'center', minWidth: 50 },
        {
          title: '',
          key: 'teacherNames',
          align: 'center',
          minWidth: 50,
          render: (h, params) => {
            let teachers = params.row.teachers
            if (teachers && teachers.length) {
              return h(
                'div',
                teachers.map((item, index) => {
                  return h(
                    'a',
                    {
                      props: {
                        href: 'javascript:void(0)'
                      },
                      on: {
                        click: () => {
                          this.$emit('showTeacherDetail', {
                            teacherEmpNo: item.teacherEmpNo,
                            cityCode: this.studentDetail.cityCode
                          })
                        }
                      }
                    },
                    item.teacherName + ' '
                  )
                })
              )
            }
          }
        },
        {
          title: '',
          key: 'tutorRealName',
          align: 'center',
          minWidth: 50
        },
        { title: '', key: 'startDate', align: 'center', minWidth: 60 },
        { title: '', key: 'endDate', align: 'center', minWidth: 60 },
        {
          title: '',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            if (
              params.row.classTimeDisplay
            ) {
              return h(
                'Poptip',
                {
                  style: {
                    display: 'inline-block',
                    marginRight: '5px'
                  },
                  props: {
                    trigger: 'hover',
                    width: '300',
                    wordWrap: true,
                    content: params.row.classTimeDisplay,
                    options: {
                      eventsEnabled: false,
                      positionFixed: true,
                      modifiers: {}
                    }
                  }
                },
                this.getStrLength(params.row.classTimeDisplay, 40, true)
              )
            } else {
              return h('span', params.row.classTimeDisplay)
            }
          }
        },
        {
          title: '',
          key: 'classroomServiceCenterName',
          align: 'center',
          minWidth: 100
        },
        { title: '', key: 'classroomName', align: 'center', minWidth: 100 },
        {
          title: '',
          align: 'center',
          minWidth: 40,
          render: (h, params) => {
            return h(
              'span',
              params.row.passedCount + '/' + params.row.classCount
            )
          }
        },
        {
          title: '',
          align: 'center',
          minWidth: 50,
          render: (h, params) => {
            return h(
              'span',
              {},
              params.row.registPersons + '/' + params.row.maxPersons
            )
          }
        },
        {
          title: '',
          key: 'surplusPersons',
          align: 'center',
          minWidth: 40
        },
        { title: '', key: 'price', align: 'center', minWidth: 40 },
        {
          title: '',
          key: 'isSchooltest',
          align: 'center',
          minWidth: 50,
          render: (h, params) => {
            return h('span', params.row.isSchooltest === 0 ? '' : '')
          }
        },
        {
          title: '',
          minWidth: 60,
          align: 'center',
          render: (h, params) => {
            if (
              params.row.classId === this.oldClassInfo.id ||
              params.row.classId === this.oldClassInfo.classId
            ) {
              return h('span', '')
            } else {
              return h('div', [
                h(
                  'Button',
                  {
                    props: {
                      size: 'small',
                      type: 'primary'
                    },
                    on: {
                      click: () => {
                        this.$emit('confirm', params.row)
                      }
                    }
                  },
                  ''
                )
              ])
            }
          }
        }
      ]
    }
  },
  mounted () {
    // 
    if (this.oldClassInfo.businessType === 1) {
      this.formData.cityId = '1111'
    } else {
      this.formData.cityId = this.studentDetail.cityCode
    }
    this.formData.subjectCustomId = this.oldClassInfo.subjectIds
    this.formData.gradeCustomId = this.oldClassInfo.gradeId
    this.formData.bookVersion = this.oldClassInfo.bookVersion
    this.getBaseData()
  },
  methods: {
    // 
    async getTimeTypeList () {
      try {
        const res = await getTimeTypeList()
        this.classTimeTypeList = Object.freeze(res)
      } catch (error) {
        this.$Message.error({ message: error.message, duration: 5 })
      }
    },
    // 
    async queryClazzDic () {
      try {
        const params = {
          platform: this.clientType,
          enName: 'limitLabel',
          cityCode: this.formData.cityId
        }
        const res = await queryClazzDic(params)
        if (res.value) {
          this.limitTags = JSON.parse(res.value)
        }
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }
    },
    getClassType (type) {
      return this.classTypeMap[type]
    },
    getStrLength (str, len, ellipsis) {
      if (!str || !len) {
        return ''
      }
      if (str.length && str.length > len && ellipsis) {
        return str.substring(0, len) + '...'
      } else {
        return str.substring(0, len)
      }
    },
    // 
    queryTargetClass () {
      if (
        this.formData.surplusPersonsStart != null &&
        this.formData.surplusPersonsEnd != null
      ) {
        if (
          this.formData.surplusPersonsStart > this.formData.surplusPersonsEnd
        ) {
          this.$Message.warning('')
          return
        }
      }

      const params = {
        ...this.formData,
        pageNo: this.pageNum,
        pageSize: this.pageSize,
        sourceClassId: this.oldClassInfo.classId,
        clientType: this.clientType,
        sellCity: this.studentDetail.cityCode
      }
      queryTargetChangeClassList(params)
        .then((res) => {
          this.tableData = res.records
          this.pageTotal = res.totalCount
          this.tableData.forEach((e) => {
            if (e.startDate) {
              e.startDate = e.startDate.substring(0, 10)
            }
            if (e.endDate) {
              e.endDate = e.endDate.substring(0, 10)
            }
          })
          this.$Message.success('')
        })
        .catch((err) => {
          console.log('', err)
          this.tableData = []
          this.$Message.warning({
            content: ':' + err,
            duration: 5
          })
        })
    },
    search () {
      this.pageNum = 1
      this.queryTargetClass()
    },
    changePage (num) {
      this.pageNum = num
      this.queryTargetClass()
    },
    pageSizeChange (size) {
      this.pageNum = 1
      this.pageSize = size
      this.queryTargetClass()
    },
    // 
    startDateChange (val) {
      if (this.formData.startDateTo) {
        if (
          new Date(val).getTime() >
          new Date(this.formData.startDateTo).getTime()
        ) {
          this.$Message.warning({
            content: '',
            duration: 5
          })
          this.formData.startDateFrom = ''
          this.startDate = ''
          return
        }
      }
      this.formData.startDateFrom = val
    },
    endDateChange (val) {
      if (this.formData.startDateFrom) {
        if (
          new Date(this.formData.startDateFrom).getTime() >
          new Date(val).getTime()
        ) {
          this.$Message.warning({
            content: '',
            duration: 5
          })
          this.formData.startDateTo = ''
          this.endDate = ''
          return
        }
      }
      this.formData.startDateTo = val
    },

    // 
    areaChange (val) {
      this.serviceCenterList = []
      this.venueList = []
      this.formData.classroomServiceCenterId = ''
      this.formData.classroomVenueId = ''
      if (!val) return
      this.getServiceCenterList('', 'noAll')
    },
    serviceChange (val) {
      this.venueList = []
      this.formData.classroomVenueId = ''
      if (!val) return
      // 
      let item = this.serviceCenterList.find((e) => {
        return e.schoolId === val
      })
      this.oldServiceName = item.name
      this.getVenueList()
    },
    changeVenue (val) {
      if (!val) return
      let item = this.venueList.find((e) => {
        return e.schoolId === val
      })
      this.oldVenueName = item.name
    },
    getServiceCenterListHandler (params) {
      return getServiceCenterList(params)
    },
    getAllServiceCenterListHandler (params) {
      return getAllServiceCenterList(params)
    },
    // 
    getServiceCenterList (name = '', type) {
      name = name.trim()
      if (!this.formData.cityId || (name && this.oldServiceName === name)) {
        return
      }
      // let req = {
      //   cityCode: this.formData.cityId,
      //   areaCode: this.formData.cityId,
      //   areaId: this.formData.classroomAreaId
      // }
      console.log(name, 'ss')
      let ser = {}
      // if (name) req.servLikeName = name
      // getServiceCenterList(req)
      //   .then((data) => {
      //     this.serviceCenterList = data
      //   })
      //   .catch((err) => {
      //     this.$Message.warning({
      //       content: ':' + err.message,
      //       duration: 5
      //     })
      //   })
      if (type === 'all') {
        ser = {
          areaCode: this.formData.cityId,
          cityCode: this.formData.cityId,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        }
        if (name) ser.likeName = name
        this.getAllServiceCenterListHandler(ser).then(res => {
          res.forEach((value, index) => {
            if (!value.address) {
              value.address = ''
            }
          })
          this.serviceCenterList = Object.freeze(res)
        }).catch(err => {
          this.$Message.warning({ content: ':' + err.message, duration: 5 })
        })
      } else {
        ser = {
          areaCode: this.formData.cityId,
          cityCode: this.formData.cityId,
          areaId: this.formData.classroomAreaId
        }
        if (name) ser.likeName = name
        this.getServiceCenterListHandler(ser).then(res => {
          res.forEach((value, index) => {
            if (!value.address) {
              value.address = ''
            }
          })
          this.serviceCenterList = Object.freeze(res)
        }).catch(err => {
          this.$Message.warning({ content: ':' + err.message, duration: 5 })
        })
      }
    },
    // 
    getVenueList (name = '') {
      name = name.trim()
      if (!this.formData.cityId || (name && this.oldVenueName === name)) {
        return
      }
      let req = {
        cityCode: this.formData.cityId,
        areaCode: this.formData.cityId,
        serviceId: this.formData.classroomServiceCenterId
      }
      if (name) req.venueName = name
      getVenueList(req)
        .then((data) => {
          this.venueList = data
        })
        .catch((err) => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 5
          })
        })
    },

    //   0 1 2
    getTeacherList (query) {
      query = query.trim()
      if (query !== '' && query !== this.oldSearchName) {
        let req = {
          schoolCode: this.formData.cityId,
          cityCode: this.formData.cityId,
          businessType: this.formData.categoryType,
          sysName: query,
          teacherType: 0,
          pageNo: 1,
          pageSize: 20
        }
        this.searchLoading = true
        getTeacherByNameFuzzy(req)
          .then((data) => {
            this.teacherList = data.records
            this.searchLoading = false
          })
          .catch((err) => {
            this.$Message.warning({
              content: ':' + err.message,
              duration: 5
            })
            this.searchLoading = false
          })
      }
    },
    getTeacherList2 (query) {
      query = query.trim()
      if (query !== '' && query !== this.oldSearchName) {
        let req = {
          schoolCode: this.formData.cityId,
          cityCode: this.formData.cityId,
          businessType: this.formData.categoryType,
          sysName: query,
          teacherType: 1,
          pageNo: 1,
          pageSize: 20
        }
        this.searchLoading = true
        getTeacherByNameFuzzy(req)
          .then((data) => {
            this.tutorTeacherList = data.records
            this.searchLoading = false
          })
          .catch((err) => {
            this.$Message.warning({
              content: ':' + err.message,
              duration: 5
            })
            this.searchLoading = false
          })
      }
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },

    // 
    gradeTypeChange (val) {
      this.formData.gradeCustomId = ''
      this.formData.bookVersion = ''
      this.gradeList = []
      this.levelList = []
      if (!val) {
        return
      }
      this.getGradeList()
    },
    // 
    gradeChange (val) {
      this.formData.bookVersion = ''
      this.levelList = []
      if (!val) {
        return
      }
      this.getLevelList()
    },
    // 
    getGradeList () {
      let req = {
        schoolCode: this.formData.cityId,
        cityCode: this.formData.cityId,
        queryType: 'g',
        years: this.oldClassInfo.year,
        termId: this.oldClassInfo.termId,
        gtIds: this.formData.gradeTypeId
      }
      getLinkage(req)
        .then((data) => {
          this.gradeList = data
        })
        .catch((err) => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 5
          })
        })
    },
    // 
    getLevelList () {
      let req = {
        schoolCode: this.formData.cityId,
        cityCode: this.formData.cityId,
        queryType: 'c',
        years: this.oldClassInfo.year,
        termId: this.oldClassInfo.termId,
        gtIds: this.formData.gradeTypeId,
        gradeIds: this.formData.gradeCustomId
      }
      getLinkage(req)
        .then((data) => {
          this.levelList = data
        })
        .catch((err) => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 5
          })
        })
    },

    // 
    getBaseData () {
      // t gt g s ,cd c 
      // 
      this.getGradeList()
      // 
      this.getLevelList()
      // 
      let req4 = {
        areaCode: this.formData.cityId,
        cityCode: this.formData.cityId
      }
      getAreaList(req4)
        .then((data) => {
          this.areaList = data
        })
        .catch((err) => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 5
          })
        })
      this.getServiceCenterList('', 'all')
      this.getVenueList()
      // 
      this.queryClazzDic()
      this.getTimeTypeList()
    }
  }
}
</script>

