import $ from '../utils/api.request'

/**
 * -
 * @param { String } studentUid 
 * @param { String } cityCode 
 * @param { String } termId 
 * @param { Number } classType 
 * @param { String } year 
 * @param { Number } type  1  2 3
 */
export function queryInvoiceList (params) {
  return $.splatAxios.request({
    url: 'invoice/queryInvoiceList',
    method: 'post',
    data: params
  })
}
