import {
  getRoleDataGrey,
  getSchoolCode
} from '../api/courseBase'
import { dateFormat } from '../utils/utils'

export default {
  data () {
    return {
      cityList: [],
      allCityMap: {}
    }
  },
  methods: {
    rendSelfIvuModalHeight (topHeight, eleSibClass, type) {
      topHeight = topHeight || 0

      // eleSibClass  sib class
      eleSibClass = eleSibClass || ''
      let eleClass = `.ov-modal-wrap-inner${eleSibClass ? ('.' + eleSibClass) : ''}`

      const fn = () => {
        const winHeight = $(window).height()
        const cssHeight = type ? 'height' : 'maxHeight'
        /*  */
        $('.ov-modal-wrap').find(eleClass).css({
          [cssHeight]: winHeight - 170 - topHeight
        })
      }

      if (window.jQuery) {
        fn()

        $(function () {
          $(window).on('resize', function () {
            fn()
          })
        })
      }
    },
    getCityListFn () {
      console.log('getCityList count --- init')
      return new Promise((resolve, reject) => {
        getRoleDataGrey({}).then((res) => {
          if (res) {
            let cityList = []
            for (let i = 0; i < res.length; i++) {
              if (res[i].dataType === 1) {
                cityList.push(res[i].dataId)
              }
            }
            if (cityList.length !== 0) {
              getSchoolCode({}).then((data) => {
                if (data) {
                  let list = data

                  if (process.env.NODE_ENV === 'development') {
                    // list = [list[0]] // 
                  }

                  /*  allCityKeyValue */
                  let map = {}
                  let arr = []
                  if (cityList[0] === 'all') {
                    for (let j = 0; j < list.length; j++) {
                      map[list[j].cityCode] = list[j].name
                      arr.push({
                        label: list[j].name,
                        value: list[j].cityCode
                      })
                    }
                  } else {
                    for (let i = 0; i < list.length; i++) {
                      map[list[i].cityCode] = list[i].name
                      for (let j = 0; j < cityList.length; j++) {
                        if (list[i].cityCode === cityList[j]) {
                          arr.push({
                            label: list[i].name,
                            value: list[i].cityCode
                          })
                          break
                        }
                      }
                    }
                  }
                  this.cityList = arr
                  this.allCityMap = map

                  sessionStorage.setItem('sessionAuthCityList', JSON.stringify(arr))
                  sessionStorage.setItem('sessionAuthAllCityMap', JSON.stringify(map))
                  resolve()
                }
              })
            }
          } else {
            reject()
            this.$Message.error('')
          }
        })
      })
    },
    getCityList () {
      return new Promise((resolve) => {
        if (this.cityList.length) {
          console.log('getCityList count --- have')
          resolve()
          return
        }

        const fn = () => {
          this.getCityListFn().then(() => {
            resolve()
          })
        }

        const sessionAuthCityList = sessionStorage.getItem('sessionAuthCityList')
        if (sessionAuthCityList) {
          try {
            this.cityList = JSON.parse(sessionAuthCityList)
            if (this.cityList && this.cityList.length) {
              console.log('getCityList count --- get')
              resolve()
            } else {
              fn()
            }
          } catch (e) {
            fn()
          }
        } else {
          fn()
        }
      })
    },
    getFormatDate (t) {
      let out = ''
      if (t) {
        t = new Date(t).getTime()
        out = dateFormat(t, 'yyyy-MM-dd')
      }
      return out
    },
    fileDownLoadAlert (platform = 'course') {
      /*
      * platform 
      * aladdin	
        auth	
        call-center 
        course	
        marketing	
        mplattraderegist	
        register	
        room	
        splat	
        teacher	
        workflow	
      * */

      let jumpText = ''
      switch (platform) {
        case 'course':
          jumpText = ''
          break
        case 'splat':
          jumpText = ''
          break
        default:
          jumpText = ''
      }

      this.$Modal.confirm({
        title: '',
        content: `<div></div><div></div><div>${jumpText}</div>`,
        onOk: () => {
          this.$emit('exportSuccess')
        }
      })
    },
    isEmptyObject (obj) {
      return Object.keys(obj).length === 0
    }
  },
  created () {
    const initConfig = (params) => {
      params = typeof params === 'string' ? {
        content: params
      } : params

      return {
        top: 50,
        closable: true,
        duration: 5,
        ...params
      }
    }

    const $M = this.$Message

    this.$Message = {
      ...$M,
      info: (params) => {
        $M.info(initConfig(params))
      },
      success: (params) => {
        $M.success(initConfig(params))
      },
      warning: (params) => {
        $M.warning(initConfig(params))
      },
      error: (params) => {
        $M.error(initConfig(params))
      }
    }
  }
}
