<!---->
<template>
  <div>
    <div style="text-align: right;margin-bottom: 10px">
      <Button type="primary" @click="addCoupon"></Button>
    </div>
    <Table stripe :columns="tableColumns" :data="tableData" :loading="tableLoading"></Table>
    <div style="margin: 10px;overflow: hidden;">
        <div style="float: right;">
          <Page
            show-elevator
            :total="totalCount"
            :current="currentPage"
            @on-change="handlePageChange"
            @on-page-size-change="handlePageSizeChange"
            :page-size="pageSize"
            show-total
            show-sizer/>
        </div>
    </div>
    <Modal
      v-model="addCouponModal"
      :mask-closable="false"
      width="600"
      :footer-hide="true"
      title=""
      class="has-tag">
      <TalAddCoupon v-if="addCouponModal" :studentId="studentDetail.studentId" :cityCode="studentDetail.cityCode" :uid="studentDetail.uid" @success="addCouponSuccess" @close="addCouponModal = false"></TalAddCoupon>
    </Modal>
    <!--  -->
    <TalCouponDetail :showCoupon="showCoupon" :releaseId="releaseId" @close="showCoupon=false" />
    <!---->
    <TalClassDetail
      :show="classDetail.show"
      :classId="classDetail.id"
      @close="classDetail.show = false">
    </TalClassDetail>
  </div>
</template>
<script>

import { getCouponList, getClassByCouponId, unBindCoupon } from '../../api/coupon'
import ClassMixin from '../../mixin/classDetail'
import { copyText } from '../../utils/utils'
import TalCouponDetail from '../tal-coupon-detail'
import TalClassDetail from '../tal-class-details'
import TalAddCoupon from '../tal-add-coupon'
export default {
  name: 'TalDiscountCoupon',
  mixins: [ClassMixin],
  components: {
    TalCouponDetail,
    TalClassDetail,
    TalAddCoupon
  },
  props: {
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data: function () {
    return {
      addCouponModal: false,
      tableLoading: false,
      modalValue: false,
      modalLoading: false,
      tableData: [], // 
      totalCount: 0,
      currentPage: 1,
      pageSize: 10,
      tableData2: [], // 
      showCoupon: false, // 
      releaseId: '', // id
      statusMap: Object.freeze({
        13: '',
        14: '',
        16: '',
        19: '',
        21: '',
        24: '',
        1: '',
        2: '',
        3: ''
      }),
      typeMap: Object.freeze({
        1: '',
        2: '22011-2012',
        3: '',
        4: '',
        5: ''
      }),
      tableColumns: Object.freeze([{
        title: '',
        align: 'center',
        minWidth: 160,
        render: (h, params) => {
          return h('div',
            [
              h('a', {
                attrs: {
                  class: 'info-btn'
                },
                style: {
                  display: 'inline-block',
                  padding: '0 5px'
                },
                on: {
                  click: () => {
                    this.unBind(params.row)
                  }
                }
              }, ' '),
              h('a', {
                attrs: {
                  class: 'info-btn'
                },
                style: {
                  display: 'inline-block',
                  padding: '0 5px'
                },
                on: {
                  click: () => {
                    this.releaseId = params.row.releaseId
                    this.showCoupon = true
                  }
                }
              }, '')
            ])
        }
      },
      {
        title: '',
        key: 'couponName',
        align: 'center'
      },
      {
        title: '',
        align: 'center',
        render: (h, params) => {
          return h('span', {
            style: {}
          }, this.typeMap[params.row.type] || '--')
        }
      },
      {
        title: '',
        key: 'couponCode',
        width: 200,
        align: 'center'
      },
      {
        title: '',
        key: 'amountName',
        align: 'center'
      },
      {
        title: '',
        align: 'center',
        render: (h, params) => {
          return h('span', {
          }, params.row.beginTime + ' / ' + params.row.expireTime)
        }
      },
      {
        title: '',
        align: 'center',
        render: (h, params) => {
          return h('span', {
            style: {}
          }, this.statusMap[params.row.couponStatus])
        }
      },
      {
        title: '',
        align: 'center',
        render: (h, params) => {
          return h('span', {
          }, params.row.isCombine === 1 ? '' : '')
        }
      },
      {
        title: '',
        align: 'center',
        key: 'refundCoupon',
        render: (h, params) => {
          return h('span', params.row.refundCoupon === 1 ? '' : '')
        }
      },
      {
        title: '',
        align: 'center',
        key: 'refundCouponTime'
      },
      {
        title: '',
        key: 'className',
        align: 'center',
        width: 200,
        render: (h, params) => {
          return h('div', [
            h('div', {
              style: {
                'text-align': 'right'
              }
            }, [
              h('Tag', {
                style: {
                  'display': params.row.businessType === 1 ? 'inline-block' : 'none'
                },
                props: {
                  color: 'green'
                }
              }, '')
            ]),
            h('a', {
              on: {
                click: () => {
                  this.showClassDetail(params.row.classId)
                }
              }
            }, params.row.className)
          ])
        }
      }])

    }
  },
  methods: {
    // 
    handlePageChange (page) {
      this.currentPage = page
      this.getTableData()
    },
    // 
    handlePageSizeChange (num) {
      this.pageSize = num
      this.currentPage = 1
      this.getTableData()
    },
    initView () {
      this.getTableData()
    },
    addCoupon () {
      if (window.XesAnalytics) {
        XesAnalytics.track('15-3629', {})
      }

      this.addCouponModal = true
    },
    addCouponSuccess () {
      this.addCouponModal = false
      this.getTableData()
    },
    getTableData () {
      this.tableLoading = true
      let req = {
        areaCode: this.studentDetail.cityCode,
        studentUid: this.studentDetail.uid,
        pageSize: this.pageSize,
        pageNo: this.currentPage
      }
      getCouponList(req, this.studentDetail.cityCode).then((res) => {
        if (res.records) {
          res.records.forEach((e, i) => {
            if (e.beginTime) {
              e.beginTime = e.beginTime.substring(0, 10)
            }
            if (e.expireTime) {
              e.expireTime = e.expireTime.substring(0, 10)
            }
          })
          this.tableData = Object.freeze(res.records)
          this.totalCount = res.totalCount
        } else {
          this.tableData = []
        }
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    unBind (row) {
      this.$Modal.confirm({
        title: '',
        content: `${row.couponCode}`,
        okText: '',
        onOk: () => {
          unBindCoupon({
            areaCode: this.studentDetail.cityCode,
            cityCode: this.studentDetail.cityCode,
            password: row.couponCode,
            studentId: this.studentDetail.studentId,
            uid: this.studentDetail.uid
          }).then(() => {
            setTimeout(() => {
              this.$Modal.success({
                title: '',
                content: `${row.couponCode}`,
                okText: '',
                onOk: () => {
                  copyText(row.couponCode)
                  this.getTableData()
                }
              })
            }, 500)
          }).catch(err => {
            this.$Message.error({
              content: '' + err.message,
              duration: 5
            })
          })
        }
      })
    },
    getClassByCouponId (item) {
      if (item.scopeType === 3) {
        this.$Message.warning({ content: '', duration: 3 })
      } else if (item.scopeType === 2) {
        this.$Message.warning({ content: '', duration: 3 })
      } else {
        this.modalValue = true
        this.modalLoading = true
        let req = {
          cityCode: this.studentDetail.cityCode,
          password: item.password
        }
        getClassByCouponId(req).then((res) => {
          res.forEach((e, i) => {
            e.index = i + 1
          })
          this.tableData2 = Object.freeze(res)
          this.modalLoading = false
        }).catch((err) => {
          this.$Message.warning({ content: ':' + err.message, duration: 5 })
          this.modalValue = false
        })
      }
    }
  }
}
</script>
