<template>
  <div>
    <!---->
    <Modal
      :mask-closable="false"
      title=""
      v-model="modalShow"
      :z-index="10000"
      @on-visible-change="onVisibleChange"
      width="960px">
      <div class="article">
        <Row>
          <Col span="10"> <span class="ivu-info-color">{{classInfo.className}}</span></Col>
          <Col span="4"> <span class="ivu-info-color">{{classInfo.subjectNames}}</span></Col>
          <Col span="4"> <span class="ivu-info-color">{{classInfo.classCount}}</span></Col>
          <Col span="4"><span class="ivu-info-color">{{classInfo.passedCount}}</span></Col>
        </Row>

        <Form
          style="margin-top: 20px"
          :label-width="100"
          label-position="left">
          <FormItem label="" required>
            <Date-picker
              :options="disableRegistTime"
              type="datetime" format="yyyy-MM-dd HH:mm:ss"
              :value="registerInfo2.payEndDate"
              @on-change="changeDate"
              placeholder="..."
            ></Date-picker>
          </FormItem>
          <FormItem label="">
            <RadioGroup v-model="registerInfo2.isContinue">
              <Radio :label=1></Radio>
              <Radio :label=0></Radio>
            </RadioGroup>
            <Select v-show="registerInfo2.isContinue===1" v-model="chooseBeforeClass" style="width: 250px;margin-left: 20px">
              <Option v-for="(item,index) in searchBeforeList" :value="item.classId" :key="index">{{ item.className }}</Option>
            </Select>
          </FormItem>
          <FormItem label="">
            <Input v-model="registerInfo2.remark" style="width: 360px;" type="textarea"/>
          </FormItem>
        </Form>
        <Row style="margin-top: 20px;">
          <Col></Col>
        </Row>
      </div>
      <div slot="footer">
        <Button type="primary" @click="save()" :loading="isLoading"></Button>
        <Button @click="modalShow = false"></Button>
      </div>
      <Modal
        :mask-closable="false"
        v-model="resultShow"
        title=""
        :z-index="10001"
        cancel-text=""
        ok-text=""
        @on-visible-change="resultVisibleChange"
        width="300">
        <p>{{resultMessage}}</p>
      </Modal>
    </Modal>
  </div>
</template>

<script>
/* eslint-disable */
import { dateFormat } from '../../utils/utils'
import { getCurrentTime } from '../../api/bd'
import {
  querySourceClass,
  regist,
  registResult,
  editRegistInfo,
  validateRegister
} from '../../api/register'

import { queryClassDetail } from '../../api/clazz'

export default {
  name: 'TalBaoClass',
  props: {
    // NoPaymentBatch 
    // BackupRecords 
    // ClassQuery 
    // NoPayment 
    from: {
      type: String,
      default: ''
    },
    show: { // 
      type: Boolean,
      default: false
    },
    isEdit: { // 
      type: Boolean,
      default: false
    },
    classId: { // id
      type: String,
      required: true
    },
    stuId: { // id
      type: String,
      required: true
    },
    stuCityCode: { // 
      type: String,
      required: true
    },
    creatorEmpNo: { // id
      type: String,
      required: true
    },
    // 
    registerInfo: {
      type: Object,
      default: function () {
        return {
          isContinue: 0, // 
          remark: '', // 
          payEndDate: '' // 
        }
      }
    }
  },
  data () {
    return {
      classInfo: {},
      isLoading: false,
      searchBeforeList: [],
      chooseBeforeClass: '',
      // 
      disableRegistTime: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now().valueOf()
        }
      },
      resultShow: false,
      resultMessage: '',
      success: false
    }
  },
  watch: {

  },
  methods: {
    onVisibleChange (status) {
      if (status) {
        this.isLoading = false
        queryClassDetail({ classId: this.classId }).then(data => {
          this.classInfo = data
        }).catch(err => {
          this.$Message.warning({ content: err.message, duration: 5 })
        })
        validateRegister({
          cityCode: this.stuCityCode,
          'X-Ca-referer': this.from,
          classId: this.classId,
          studentId: this.stuId,
          type: 1 // (1:;2:;3:;4:)
        }).then(data => {
          const rlt = data[0]
          if (rlt.code !== '000000') {
            let msg = ''
            switch (rlt.code) {
              case '555555':
                msg = ''
                break
              case '52212':
                msg = ''
                break
              case '51907':
                msg = ''
                break
              case '51908':
                msg = '' + rlt.message
                break
              case '52210':
                msg = '' + rlt.message
                break
              default:
                msg = ''
            }
            this.$Message.warning({
              content: msg,
              duration: 5
            })
          }
        }).catch(err => {
          this.$Message.error({
            content: `${err.message}`,
            duration: 5
          })
        })
      }
    },
    resultVisibleChange (status) {
      if(!status) {
        this.resultMessage = ''
        if (this.success) {
          this.modalShow = false
        }
      }
    },
    changeDate (data) {
      this.registerInfo2.payEndDate = data
    },
    save () {
      if (!this.registerInfo2.payEndDate) {
        this.$Message.warning('')
        return
      }
      // if (!this.registerInfo2.remark) {
      //   this.$Message.warning('')
      //   return
      // }
      if (this.isEdit) {
        let req = {
          cityCode: this.stuCityCode,
          'X-Ca-referer': this.from,
          uid: this.registerInfo.uid,
          payEndDate: this.registerInfo2.payEndDate, // 
          remark: this.registerInfo2.remark, // 
          registId: this.registerInfo2.registId,
          isContinue: this.registerInfo2.isContinue, //  0:;1:;2:;3:;4:;5:,0
          sourceClassId: this.registerInfo2.isContinue === 1 ? this.chooseBeforeClass : undefined // id
        }
        if (req.isContinue === 1) {
          if (!req.sourceClassId) {
            this.$Message.warning({ content: '', duration: 5 })
            return
          }
        }
        this.isLoading = true
        editRegistInfo(req).then(res => {
          this.$Message.success('')
          this.$emit('success')
          this.isLoading = false
          this.modalShow = false
        }).catch(err => {
          this.$Message.warning({ content: ': ' + err.message, duration: 5 })
          this.isLoading = false
        })
      } else {
        let req = {
          studentId: this.stuId,
          cityCode: this.stuCityCode,
          'X-Ca-referer': this.from,
          classId: this.classId,
          creatorEmpNo: this.creatorEmpNo,
          remark: this.registerInfo2.remark, // 
          type: this.registerInfo2.isContinue, //  0:;1:;2:;3:;4:;5:,0
          sourceClassId: this.registerInfo2.isContinue === 1 ? this.chooseBeforeClass : '', // id
          payEndDate: this.registerInfo2.payEndDate // 
        }
        if (req.type === 1) {
          if (!req.sourceClassId) {
            this.$Message.warning({ content: '', duration: 5 })
            return
          }
        }
        this.isLoading = true
        regist(req).then(data => {
          if (data.execResult !== '2') {
            const int = setInterval(() => {
              registResult({
                cityCode: this.stuCityCode,
                'X-Ca-referer': this.from,
                studentId: this.stuId,
                classId: this.classId,
                registId: data.registId
              }).then(rr => {
                if (rr.execResult !== '0') {
                  clearInterval(int)
                  if (rr.execResult === '1') {
                    this.success = true
                    this.resultMessage = ''
                    this.$emit('success')
                  } else if (rr.execResult === '2') {
                    this.success = false
                    this.resultMessage = `${rr.message}`
                    this.$emit('fail')
                  }
                  this.resultShow = true
                  this.isLoading = false
                }
              }).catch(err => {
                clearInterval(int)
                this.isLoading = false
                console.error(err)
              })
            }, 1000)
          } else {
            this.success = false
            this.resultMessage = `${data.message}`
            this.resultShow = true
            this.$emit('fail')
            this.isLoading = false
          }
        }).catch(err => {
          this.success = false
          this.resultMessage = `${err.message}`
          this.resultShow = true
          this.isLoading = false
        })
      }
    },
    isContinueChange () {
      let req = {
        cityCode: this.stuCityCode,
        'X-Ca-referer': this.from,
        studentId: this.stuId,
        type: 0,
        year: this.classInfo.year,
        termId: this.classInfo.termId
      }
      querySourceClass(req).then(res => {
        if (res.length <= 0) {
          this.$Message.warning('')
        } else {
          this.searchBeforeList = res
          // 
          if (this.registerInfo2.sourceClassId) {
            this.chooseBeforeClass = this.registerInfo2.sourceClassId
          } else {
            this.chooseBeforeClass = this.searchBeforeList[0].classId
          }
        }
      }).catch(err => {
        this.$Message.error(':' + err.message)
      })
    }
  },
  computed: {
    modalShow: {
      get: function () {
        if (this.show === true) {
          if (!this.isEdit) {
            this.registerInfo.isContinue = 0
            this.registerInfo.sourceClassId = ''
            this.registerInfo.remark = ''
            getCurrentTime().then(data => {
              this.registerInfo.payEndDate = dateFormat(data + 24 * 60 * 60 * 1000, 'yyyy-MM-dd HH:mm:ss')
            }).catch(err => {
              this.$Message.error({
                content:'' + err.message,
                duration: 5
              })
            })
          }
        }
        return this.show
      },
      set: function (val) {
        if (val === false) {
          this.searchBeforeList = []
          this.chooseBeforeClass = ''
          this.$emit('close')
        }
      }
    },
    registerInfo2: {
      get: function () {
        if (this.registerInfo.isContinue === 1) {
          this.isContinueChange()
        }
        return this.registerInfo
      },
      set: function () {

      }
    }
  }
}
</script>

<style scoped>
.changeColor {
  color: orange;
}
</style>
