<script>
import { verificationCodeSend, checkPhoneLastLogout } from '../api/student'

export default {
  data () {
    return {
      sendBtnT: false, // 
      msgNumT: '', // 
      smsCodeT: '', // 
      hasLoggedOut: false, // -Modal
      // registerReason: '', // 
      loggedOutTime: '', // 
      lastLoggedOut: false
    }
  },
  methods: {
    // 
    sendMessageHandlerT (phone, mode) {
      if (!this.sendBtnT) {
        if (!phone) {
          this.$Message.warning({
            content: '',
            duration: 3
          })
          return
        }
        this.sendBtnT = true
        this.msgNumT = 30
        let set
        let reqForChecking = {
          phone
        }
        checkPhoneLastLogout(reqForChecking)
          .then(res => {
            if (res !== '') {
              this.$Message.warning({ content: '', duration: 3 })
              this.hasLoggedOut = true
              this.loggedOutTime = res
            } else {
              let req = {
                phone,
                mode // regCodebindCode
              }
              verificationCodeSend(req)
                .then(res => {
                  this.$Message.success({ content: '', duration: 3 })
                  set = setInterval(() => {
                    if (this.msgNumT <= 0) {
                      clearInterval(set)
                      this.sendBtnT = false
                    } else {
                      this.msgNumT -= 1
                    }
                  }, 1000)
                }).catch(err => {
                  clearInterval(set)
                  this.sendBtnT = false
                  this.$Message.error({
                    content: err.message,
                    duration: 5
                  })
                })
            }
          }).catch(err => {
            this.sendBtnT = false
            this.$Message.error({
              content: err.message,
              duration: 5
            })
          })
      }
    },
    resetSendBtn () {
      this.sendBtnT = false
    }
  }
}
</script>
