import $ from '../utils/api.request'

const masterBD = $.masterBDAxios

/*  */
export const queryAreaList = params => {
  return masterBD.request({
    url: '/area/list',
    method: 'get',
    params: params
  })
}

/*  */
export const queryServiceCenterList = params => {
  return masterBD.request({
    url: '/serviceCenter/list',
    method: 'get',
    params: params
  })
}

/*  */
export const queryVenueList = params => {
  return masterBD.request({
    url: '/venue/list',
    method: 'get',
    params: params
  })
}

/*  */
export const queryClassroomList = params => {
  return masterBD.request({
    url: '/classroom/list',
    method: 'GET',
    params: params
  })
}

/*  */
export const queryClassLevelList = params => {
  return masterBD.request({
    url: '/classlevel/list',
    method: 'post',
    data: params
  })
}

/**
 * 
 * @returns 
 */
export const queryAllSchoolList = () => {
  return masterBD.request({
    method: 'get',
    url: '/organ/findList'
  })
}
