<template>
  <div class="invoice-query">
    <Form
      :label-width="75"
      label-position="left"
      ref="form"
      :model="formData"
      :rules='ruleData'
      inline
      style="text-align: left"
    >
      <FormItem label="" prop="areaCode">
        <Select v-model="formData.areaCode" placeholder="" clearable class="w120" filterable>
          <Option v-for="(item) in allAreaCode" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="businessCode">
        <Select v-model="formData.businessCode" placeholder="" clearable class="w120">
          <Option v-for="(item) in allBusiness" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="queryType" :label-width="85">
        <Select v-model="formData.queryType" placeholder="" clearable class="w120">
          <Option v-for="(item) in feeTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="invoiceType">
        <Select v-model="formData.invoiceType" placeholder="" clearable class="w120" filterable>
          <Option v-for="(item, key) in invoiceTypeList" :value="item.id" :key="key">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="invoiceStatus">
        <Select v-model="formData.invoiceStatus" placeholder="" clearable class="w120">
          <Option v-for="(item, key) in invoiceStateList" :value="item.id" :key="key">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="source" :label-width="65">
        <Select v-model="formData.source" placeholder="" clearable class="w120">
          <Option v-for="(item, key) in sourceList" :value="item.id" :key="key">{{ item.name }}</Option>
        </Select>
      </FormItem>
        <FormItem label=""  >
          <Input v-model="formData.studentName" placeholder="" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label=""  >
          <Input v-model="formData.studentNo" placeholder="" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="" :label-width="85">
          <Input v-model="formData.operatorName" placeholder="" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="">
          <Input v-model="formData.invoiceNum" placeholder="" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="">
          <Input v-model="formData.invoiceCode" placeholder="" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="" :label-width="85">
          <Input v-model="formData.originalInvoiceNum" placeholder="" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="" :label-width="85">
          <Input v-model="formData.originalInvoiceCode" placeholder="" :maxlength="400" clearable class="w120"/>
        </FormItem>
        <FormItem label="">
          <Date-picker
            type="date"
            :value="formData.invoiceTime"
            placeholder=""
            placement="bottom-end"
            format="yyyy-MM-dd"
            @on-change="changeDate"
            class="w120"
          ></Date-picker>
        </FormItem>
        <FormItem label="ID">
          <Input v-model="formData.subOrderId" placeholder="" :maxlength="400" clearable class="w120 ml10"/>
        </FormItem>
        <FormItem label="">
          <Input v-model="formData.subjectName" placeholder="" :maxlength="400" clearable class="w120 ml10"/>
        </FormItem>
      <div class="invoice-all-data">
        <p>
          {{tableList.countBlue || 0}}{{tableList.sumBlueAmount || 0}}{{tableList.countRed || 0}}{{tableList.sumRedAmount || 0}}
        </p>
        <div class="invoice-all-button">
          <Button type="primary" @click="getTableData(true)" :loading="tableLoading"></Button>
          <Button type="primary" style="margin-left: 15px;" v-if="importButtonFlag" @click="importTableData"></Button>
        </div>
      </div>
    </Form>
    <Table style="width: 100%;margin-top: 10px" :columns="cols" :data="tableList.data" :loading="tableLoading">
      <template slot-scope="{ row }"  slot="sp">
        <a @click="getDetailData(row)"></a>
      </template>
      <template slot-scope="{ row }"  slot="invoiceType">
        {{row.invoiceType|filterInvoiceName}}
      </template>
      <template slot-scope="{ row }"  slot="invoiceSubjectType">
        {{row.invoiceSubjectType|filterInvoiceTypeName}}
      </template>
       <template slot-scope="{ row }"  slot="source">
        {{{source: row.source, sourceList: sourceList}|filterSourceName}}
      </template>
      <template slot-scope="{ row, index }"  slot="index">
        {{(formData.pageNum - 1) * formData.pageSize + index + 1}}
      </template>
      <template slot-scope="{ row }"  slot="buyer">
        {{row.buyer || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="createTime">
        {{row.createTime|formatTimeFilter}}
      </template>
      <template slot-scope="{ row }"  slot="originalInvoiceCode">
        {{row.originalInvoiceCode || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="originalInvoiceNum">
        {{row.originalInvoiceNum || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="receiver">
        {{row.receiver || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="invoiceCode">
        {{row.invoiceCode || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="invoiceNum">
        {{row.invoiceNum || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="invoiceRate">
        {{row.invoiceRate|filterRate}}
      </template>
      <template slot-scope="{ row }"  slot="buyerIdentifyNum">
        {{row.buyerIdentifyNum || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="buyerAddressPhone">
        {{row.buyerAddressPhone || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="buyerOpeningBank">
        {{row.buyerOpeningBank || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="invoiceId">
        {{row.invoiceId || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="subOrderId">
        {{row.subOrderId || '--'}}
      </template>
      <template slot-scope="{ row }"  slot="resultMsg">
        {{row.resultMsg || '--'}}
      </template>
    </Table>
    <div style="text-align: right;margin-top: 10px;">
      <Page
        :total="tableList.count"
        :page-size="formData.pageSize"
        :current.sync="formData.pageNum"
        show-elevator
        show-sizer
        show-total
        @on-page-size-change="pageSizeChange"
        @on-change="changePage"></Page>
    </div>
    <div v-if="this.formData.queryType !== 3">
      <detail
      :value="detailFlag"
      :inDetailData="inDetailData"
      :ifRedDashed="ifRedDashed"
      @close="closeModal"
      ></detail>
    </div>
    <div v-if="this.formData.queryType === 3">
      <scholarshipDetail
      :value="detailFlag"
      :inDetailData="inDetailData"
      :ifRedDashed="ifRedDashed"
      @close="closeModal"
      ></scholarshipDetail>
    </div>
  </div>
</template>

<script>
import detail from './detailModal/detail'
import scholarshipDetail from '../tal-invoice-query-detail-scholarship/detail'
import filters from '../../filters/index'
export default {
  name: 'talInvoiceQuery',
  data () {
    return {
      formData: {
        areaCode: '',
        businessCode: '',
        queryType: '',
        invoiceStatus: '',
        source: '',
        invoiceType: '',
        studentName: '',
        studentNo: '',
        operatorName: '',
        invoiceNum: '',
        invoiceCode: '',
        originalInvoiceNum: '',
        originalInvoiceCode: '',
        invoiceTime: '',
        subOrderId: '',
        subjectName: '',
        pageSize: 10,
        pageNum: 1
      },
      searchLoading: false,
      cols: [
        { title: '', align: 'center', width: 85, slot: 'sp' },
        { title: '', align: 'center', width: 60, slot: 'index' },
        { title: '', align: 'center', width: 100, key: 'areaCode' },
        { title: '', align: 'center', width: 100, key: 'businessCode' },
        { title: '', align: 'center', width: 100, slot: 'invoiceId' },
        {
          // title: 'ID',
          align: 'center',
          width: 120,
          slot: 'subOrderId',
          renderHeader: (h, params) => {
            return h('div', [
              h('p', {
                style: {
                  textAlign: 'center'
                }
              }, this.formData.queryType !== 3 ? 'ID' : 'ID')
            ])
          }
        },
        { title: '', align: 'center', width: 120, slot: 'invoiceCode' },
        { title: '', align: 'center', width: 120, slot: 'invoiceNum' },
        { title: '', align: 'center', width: 150, key: 'sellerName' },
        { title: '', align: 'center', width: 90, key: 'invoiceAmount' },
        { title: '', align: 'center', width: 60, slot: 'invoiceRate' },
        { title: '', align: 'center', width: 150, slot: 'invoiceSubjectType' },
        { title: '', align: 'center', width: 120, slot: 'buyer' },
        { title: '', align: 'center', width: 170, slot: 'buyerIdentifyNum' },
        { title: '', align: 'center', width: 150, slot: 'buyerAddressPhone' },
        { title: '', align: 'center', width: 110, slot: 'buyerOpeningBank' },
        { title: '', align: 'center', width: 165, slot: 'createTime' },
        { title: '', align: 'center', width: 90, slot: 'invoiceType' },
        { title: '', align: 'center', width: 90, key: 'invoiceStateDesc' },
        { title: '', align: 'center', width: 110, slot: 'source' },
        { title: '', align: 'center', width: 280, slot: 'resultMsg' },
        { title: '', align: 'center', width: 100, slot: 'originalInvoiceCode' },
        { title: '', align: 'center', width: 100, slot: 'originalInvoiceNum' },
        { title: '', align: 'center', width: 100, key: 'operatorName' },
        { title: '', align: 'center', width: 100, slot: 'receiver' },
        { title: '', align: 'center', width: 160, key: 'receiverEmail' },
        { title: '', align: 'center', width: 120, key: 'receiverPhone' },
        { title: '', align: 'center', width: 90, key: 'studentName' },
        { title: '', align: 'center', width: 90, key: 'studentNo' }
      ],
      tableLoading: false,
      ruleData: {
        areaCode: [
          { required: true, message: '', trigger: 'change' }
        ],
        businessCode: [
          { required: true, message: '', trigger: 'change' }
        ],
        queryType: [
          { required: true, message: '', trigger: 'change', type: 'number' }
        ]
      },
      detailFlag: false,
      invoiceTypeList: [
        { id: 0, name: '' },
        { id: 1, name: '' }
      ],
      invoiceStateList: [
        { id: 1, name: '' },
        { id: 2, name: '' },
        { id: 3, name: '' },
        { id: 4, name: '' },
        { id: 5, name: '' },
        { id: 6, name: '' },
        { id: 7, name: '' },
        { id: 8, name: '' }
      ]
    }
  },
  components: {
    detail,
    scholarshipDetail
  },
  filters: filters,
  props: {
    classDetailMessage: {
      type: Object,
      default: function () {
        return {}
      }
    },
    importButtonFlag: { // 
      type: Boolean,
      default: true
    },
    ifRedDashed: { // 
      type: Boolean,
      default: false
    },
    allAreaCode: { // 
      type: Array,
      default () {
        return []
      }
    },
    allBusiness: { // 
      type: Array,
      default () {
        return []
      }
    },
    feeTypeList: {
      type: Array,
      default () {
        return []
      }
    },
    sourceList: {
      type: Array,
      default () {
        return []
      }
    },
    tableList: {
      type: Object,
      default: function () {
        return {
          count: 0,
          data: [],
          countBlue: 0,
          sumBlueAmount: 0,
          countRed: 0,
          sumRedAmount: 0
        }
      }
    },
    inDetailData: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    pageSizeChange (val) {
      this.formData.pageNum = 1
      this.formData.pageSize = val
      this.getTableData()
    },
    changePage (val) {
      this.formData.pageNum = val
      this.getTableData()
    },
    getTableData (mark) {
      // 
      this.$refs['form'].validate((valid) => {
        if (valid) {
          if (mark) {
            this.formData.pageNum = 1
            this.tableLoading = true
          }
          this.$emit('getInvoiceList', this.formData, () => {
            this.tableLoading = false
          })
        }
      })
    },
    getDetailData (row) {
      let params = {
        invoiceId: row.invoiceId
      }
      this.$emit('getInvoiceDetail', params, this.formData.queryType, () => {
        this.detailFlag = true
      })
    },
    importTableData () {
      // 
      if (this.tableList.data.length === 0) {
        this.$Message.warning('')
        return
      }
      this.$emit('importInvoiceList', this.formData)
    },
    closeModal (val) {
      this.detailFlag = val
    },
    changeDate (date) {
      this.formData.invoiceTime = date
    }
  }
}
</script>

