import $ from '../utils/api.request'

const auth = $.authAxios

/* oss  */
export const uploadGetAuth = params => {
  return auth.request({
    url: '/upload/getAuth',
    method: 'get',
    params: params
  })
}

/* oss  */
export const uploadGetUrl = params => {
  return auth.request({
    url: 'upload/getUrl',
    method: 'get',
    params: params
  })
}

/* oss  */
export const uploadCancel = params => {
  return auth.request({
    url: 'upload/cancel',
    method: 'get',
    params: params
  })
}
