                      <div class="ivu-table-cell">
                        <a @click="$emit('apply', clazz)"></a><br>
                        <a @click="$emit('reserveClass', clazz)"></a><br>
                        <a @click="$emit('enterClass', clazz)"></a>
                      </div>
                    </td>
                    <td class="ivu-table-column-center">
                      <div class="ivu-table-cell">
                        <a @click="$emit('showClassDetail', clazz.id)">{{clazz.name}}</a>
                        <Tag color="green" v-if="clazz.businessType==1"></Tag>
                        <Tag color="orange">{{clazz.recommendRemark}}</Tag>
                      </div>
                    </td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ classTypeMap[clazz.classType] }}</div></td>
                    <td class="ivu-table-column-center"><div class="ivu-table-cell">{{ clazz.subjectNames }}</div></td>
                    <td class="ivu-table-column-center">
