      </TabPane>
    </Tabs>
  </div>
</template>

<script>
import { getRecommendClassInfo } from '../../api/clazz'
export default {
  name: 'TalRecommendClass',
  data () {
    return {
      dataList: [],
      classTypeMap: {
        1: '',
        2: '',
        4: ''
      },
      activeTab: 'tabName0'
    }
  },
  props: {
    params: Object
  },
  methods: {
    initView () {
      this.activeTab = 'tabName0'
      this.dataList = []
      this.$emit('setHasRecommend', false)
    },
    search () {
      this.activeTab = 'tabName0'
      this.$nextTick(() => {
        this.params.business = 'recommend'
        getRecommendClassInfo(this.params).then(data => {
          if (data && data.length) {
            this.$emit('setHasRecommend', true)
            this.dataList = data
          } else {
            this.$emit('setHasRecommend', false)
            this.dataList = []
          }
        }).catch(err => {
          this.$emit('setHasRecommend', false)
          this.dataList = []
          this.$Message.error({
            content: '' + err.message,
            duration: 5
          })
        })
      })
    }
  }
}
</script>

