<!---->
<template>
  <div class="preferential">
    <template v-if="promotionList.length===0">
      <Row class="no-data">
        <Col span="24"></Col>
      </Row>
    </template>
    <template v-else>
      <div v-for="(promotion,index) in promotionList" :key="promotion.promotionId">
        <h2 style="margin-left:-30px">{{index+1}}</h2>
        <Row class="border-dash">
          <Col span="3" class="spa">
            :
          </Col>
          <Col span="21" class="mess">
            {{promotion.promotionName}}
          </Col>
        </Row>
        <Row class="border-dash">
          <Col span="3" class="spa">
            :
          </Col>
          <Col span="21" class="mess">
            {{promotion.promotionTypeName}}
          </Col>
        </Row>
        <Row class="border-dash">
          <Col span="3" class="spa">
            :
          </Col>
          <Col span="21" class="mess">
            {{promotion.startTime}}
          </Col>
        </Row>
        <Row class="border-dash">
          <Col span="3" class="spa">
            :
          </Col>
          <Col span="21" class="mess">
            {{promotion.endTime}}
          </Col>
        </Row>
        <template v-if="typeOne(promotion.promotionType)">
          <template v-for="(promotionClassGroupList) in promotion.mainClassGroupList">
            <template v-for="(item, index) in promotionClassGroupList">
              <Row class="info" :key="item.id">
                <Col span="2" class="spa">
                  <template v-if="index === 0">
                    :
                  </template>
                  <template>
                    &nbsp;
                  </template>
                </Col>
                <Col span="2" class="spa">
                  <template v-if="index === 0">
                    :
                  </template>
                  <template>
                    &nbsp;
                  </template>
                </Col>
                <Col span="20" class="mess">
                  <p>{{buildClassInfo(item)}}</p>
                </Col>
              </Row>
            </template>
          </template>
          <template v-for="(item, index) in promotion.ruleClassGroupList">
            <div :key="index">
            <Row class="info">
              <Col span="2" class="spa">
                
              </Col>
              <Col span="2" class="spa">
                {{pType(promotion.promotionType)}}
              </Col>
              <Col span="20" class="spa">
                {{getPrice(item.rule, promotion.promotionType)}}
              </Col>
            </Row>
            <Row class="info">
              <Col span="2" class="spa">
                &nbsp;
              </Col>
              <Col span="2" class="spa">
                
              </Col>
              <Col span="20" class="mess">
                <template v-for="(promotionClass,index1) in item.promotionClassList">
                  <p :key="index1">{{buildClassInfo(promotionClass)}}</p>
                </template>
              </Col>
            </Row>
            </div>
          </template>
        </template>

        <template v-for="(item, index) in promotion.ruleList">
          <Row class="info" :key="index">
            <Col span="3" class="spa">
              <template v-if="index === 0">
                
              </template>
              <template>
                &nbsp;
              </template>
            </Col>
            <Col span="21" class="mess">
              <p>{{buildRule(promotion.promotionType, item)}}</p>
            </Col>
          </Row>
        </template>
        <template v-if="promotion.mainClassGroupList && promotion.promotionType === 16">
          <template v-for="promotionClassGroupList in promotion.mainClassGroupList">
            <template v-for="(item, index) in promotionClassGroupList">
              <Row class="info" :key="item.id">
                <Col span="3" class="spa">
                  <template v-if="index === 0">
                    
                  </template>
                  <template>
                    &nbsp;
                  </template>
                </Col>
                <Col span="21" class="mess">
                  <p>{{buildClassInfo(item)}}</p>
                </Col>
              </Row>
            </template>
          </template>
        </template>
        <template v-if="promotion.promotionClassGroupList">
          <template v-for="promotionClassGroupList in promotion.promotionClassGroupList">
            <template v-for="(item, index) in promotionClassGroupList">
              <Row class="info" :key="item.id">
                <Col span="3" class="spa">
                  <template v-if="index === 0">
                    {{promotion.promotionType === 16 ?  (promotion.mainClassGroupList && promotion.mainClassGroupList.length ? '' : '') : ''}}
                  </template>
                  <template>
                    &nbsp;
                  </template>
                </Col>
                <Col span="21" class="mess">
                  <p>{{buildClassInfo(item)}}</p>
                </Col>
              </Row>
            </template>
          </template>
        </template>
      </div>
    </template>
  </div>
</template>

<script>
/**
   * promotionType 10- 11- 12-  13- 14- 15- 16-
   */
import {
  queryClassPromotionActivity
} from '../../../api/clazz'
const PROMOTION = {
  LINKDISCOUNT: 10,
  ADD: 11,
  LINKSUB: 12,
  TWODISCOUNT: 13,
  TWOSUB: 14,
  TIMELIMIT: 15,
  BUYGIVING: 16
}
export default {
  name: 'preferentialInfo',
  props: {
    classInfo: {
      type: Object,
      require: true
    }
  },
  data () {
    return {
      loading: false,
      promotionType: {
        10: '',
        11: '',
        12: '',
        13: '',
        14: '',
        15: '',
        16: ''
      },
      promotionList: []
    }
  },
  computed: {

  },
  methods: {
    initView () {
      this.promotionList = []
      this.getData()
    },
    getData () {
      this.loading = true
      let req = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id
      }
      queryClassPromotionActivity(req).then(res => {
        if (res) {
          this.promotionList = res
        }
      }).catch(err => {
        // this.$Message.warning({ content: ':' + err, duration: 5 })
        this.$emit('errMsg', ':' + err)
      }).finally(() => {
        this.loading = false
      })
    },
    //     
    typeOne: function (type) {
      return [PROMOTION.ADD, PROMOTION.TWODISCOUNT, PROMOTION.TWOSUB].indexOf(Number(type)) >= 0
    },
    // 
    pType: function (type) {
      if (type === PROMOTION.ADD) {
        return ''
      } else if (type === PROMOTION.TWODISCOUNT) {
        return ''
      } else if (type === PROMOTION.TWOSUB) {
        return ''
      }
      return ' '
    },
    getPrice (item, type) {
      if (type === PROMOTION.ADD) {
        return item.increaseAmount + ''
      } else if (type === PROMOTION.TWODISCOUNT) {
        return item.discount + '%'
      } else if (type === PROMOTION.TWOSUB) {
        return item.reduceAmount + ''
      }
    },
    buildClassInfo (data) {
      /**
      classLevelId: "219"
      classLevelName: "6688"
      classType: 2
      classTypeName: ""
      gradeId: "8"
      gradeName: ""
      gtId: "2"
      gtName: ""
      subjectId: "ff80808127d77caa0127d7e10f1c00c4"
      subjectName: ""
      termId: "4"
      termName: ""
      year: "2019"
       */
      return [
        `(${data.classTypeName})`,
        `${data.year}`,
        data.termName,
        data.gtName,
        data.gradeName,
        data.subjectName,
        data.classLevelName
      ].join('')
    },
    buildRule (type, data) {
      if (type === PROMOTION.BUYGIVING) {
        return ` ${data.buyCount}  ${data.giveCount} `
      } else if (type === PROMOTION.TIMELIMIT) {
        let res = ` ${data.startPeriod}  ${data.endPeriod} `
        if (data.reduceAmount) {
          res += ` ${data.reduceAmount} `
        } else if (data.discount) {
          res += ` ${data.discount} %`
        } else if (data.fixedAmount) {
          res += ` ${data.fixedAmount} `
        }
        return res
      } else if (type === PROMOTION.LINKSUB) {
        return `  ${data.buyCount}  ${data.reduceAmount} `
      } else if (type === 10) {
        return `  ${data.buyCount}  ${data.discount} %`
      }
    }
  }
}
</script>

