<template>
  <div class="upload-box">
    <template v-if="type === 'single'">
      <Button v-show="!fileList[0]" :id="btnId" class="upload-btn" :loading="uplaodIng" :disabled="disabled">
        <span style="margin-left: 5px;">{{ btnName }}</span>
      </Button>
      <div class="progress-container" v-if="fileList[0]">
        <div class="info-box flex" style="position: relative;">
          <i class="iconfont icon-attachment" style="margin-right: 10px;"></i>
          <span class="link-name" :style="{'max-width': textMaxWidth, 'min-width': textMinWidth}">{{fileList[0].name}}</span>
          <i class="iconfont icon-delete" style="margin-left: 20px;" @click="handleRemove(fileList[0].name)"></i>
        </div>
        <!--  -->
        <Progress v-if="fileList[0].percent !== 101" :percent="fileList[0].percent" hide-info :stroke-width="2" :stroke-color="fileList[0].status === 4 ? '#EB4245' : '#3B7EFF' " />
      </div>
    </template>

    <!-- <div :id="btnId" class="upload-btn">
      <Icon size="20" type="ios-cloud-upload-outline" />
      <div class="prevent" v-if="fileList.length >= maxLength" @click.stop="handleClick"></div>
    </div> -->
    <!-- <div class="attachement-list" style="margin-top: 20px;">
      <div
        class="attachement-list-item"
        style="width: 450px;"
        v-for="(item) in fileList"
        :key="item.key"
      >
        <a
          class="flex"
          :href="item.status === 'finished' ? item.path : 'javascript:void(0)'"
          :target="item.status === 'finished' ? '_blank' : ''"
        >
          <div>
            <Icon size="14" type="ios-link" />
            {{ item.name }}
          </div>
          <Icon @click.prevent="handleRemove(item.name)" size="24" type="ios-close" />
        </a>
        <Progress :percent="item.percent" hide-info :stroke-width="2" stroke-color="#2d8cf0" />
      </div>
    </div> -->
  </div>
</template>

<script>
import plupload from 'plupload'
import { uploadGetAuth } from '../../api/auth'
import { runEnv, dateFormat } from '../../utils/utils'
import Cookie from 'js-cookie'

export default {
  name: 'TalOssUploadUi',
  props: {
    value: {
      type: Array,
      default: () => []
    },
    btnName: {
      type: String,
      default: ''
    },
    // 
    required: {
      type: Boolean,
      default: false
    },
    // 
    empNo: {
      type: String,
      default: JSON.parse(Cookie.get('columbusUserInfo')).id
    },
    // ,
    platform: {
      type: String,
      required: true
    },
    // or
    type: {
      type: String,
      default: 'single' // mutil
    },
    // 
    maxFileSize: {
      type: [Number, String],
      default: '5mb'
    },
    // ,
    multiSelection: {
      type: Boolean,
      default: false
    },
    // 
    multiLength: {
      type: Number,
      default: 1
    },
    // 
    maxLength: {
      type: Number,
      default: 99
    },
    // 
    extensions: {
      type: Array,
      default: function () {
        return [
          {
            title: 'mime supprot',
            extensions:
              'pptx,ppt,pptm,xls,xlt,xlsx,xltx,xlsb,xlsm,xltm,doc,dot,wps,docx,dotx,docm,dotm,pdf,txt,jpg,png,pdf,jpeg,PPTX,PPT,PPTM,XLS,XLT,XLSX,XLTX,XLSB,XLSM,XLTM,DOC,DOT,WPS,DOCX,DOTX,DOCM,DOTM,PDF,TXT,JPG,PNG,PDF,JPEG'
          }
        ]
      }
    },
    // 
    textMaxWidth: {
      type: String,
      default: '990'
    },
    textMinWidth: {
      type: String,
      default: '150'
    }
  },
  watch: {
    fileList () {
      this.$emit('input', this.fileList)
    },
    value () {
      this.fileList = this.value
    }
  },
  data () {
    const btnId = 'upload_btn_' + Math.random() * 10000
    return {
      btnId,
      uplaodIng: false,
      disabled: false,
      fileList: []
    }
  },
  mounted () {
    this.fileList = [...this.value]
    // dom
    setTimeout(() => {
      if (this.type === 'single') {
        this.initSingleUploadFile()
      }
    }, 500)
  },
  methods: {
    // 
    validate () {
      return new Promise((resolve, reject) => {
        if (this.fileList.length === 0 && this.required) {
          return reject(new Error(''))
        }
        let isOk = true
        this.fileList.forEach((item) => {
          if (item.status !== 5) {
            isOk = false
          }
        })
        if (isOk) {
          resolve()
        } else {
          reject(new Error(''))
        }
      })
    },
    guid () {
      return 'xxxxxxxxxxxx4xxxyxxxxxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        let r = (Math.random() * 16) | 0
        let v = c === 'x' ? r : (r & 0x3) | 0x8
        return v.toString(16)
      })
    },
    handleRemove (fileName) {
      // 
      this.uploader.files.forEach((f, i) => {
        if (f.name === fileName) {
          this.uploader.splice(i, 1)
        }
      })
      this.uplaodIng = false
      this.fileList = this.fileList.filter((f) => f.name !== fileName)
      this.$emit('on-change', this.fileList)
    },
    // 
    initSingleUploadFile () {
      // File.status 1 QUEUED 2 UPLOADING 4 FAILED 5 DONE
      let that = this
      let uploader = (this.uploader = new plupload.Uploader({
        runtimes: 'html5,html4',
        browse_button: that.btnId, // id
        multi_selection: that.type === 'single' ? false : that.multiSelection, // 
        url: `${runEnv() === 'prod' ? 'https' : 'http'}://oss.aliyuncs.com`, // url
        filters: {
          mime_types: that.extensions, // 
          max_file_size: that.maxFileSize, // 5mb
          prevent_duplicates: false // 
        },
        max_retries: 3, // 
        init: {
          PostInit: function () {
            // Init
          },
          FilesAdded: async function (up, files) {
            if (files.length > that.multiLength) {
              that.$Message.warning({
                content: `${that.multiLength}`,
                duration: 5
              })
              return
            }
            that.uplaodIng = true
            try {
              const authObj = await uploadGetAuth({
                fileName: files[0].name,
                empNo: that.empNo,
                systemName: that.platform
              })
              that.authObj = authObj
              let hasNewFile = false
              files.forEach((file) => {
                let flag =
                  that.fileList.filter((f) => f.name === file.name).length > 0
                if (flag) {
                  that.$Message.warning({
                    content: `${file.name}`,
                    duration: 5
                  })
                  up.removeFile(file)
                } else {
                  hasNewFile = true
                  file.key =
                    authObj.dir +
                    dateFormat(Date.now(), 'HH:mm:ss') +
                    '-' +
                    file.name
                  that.fileList.push({
                    path: `${authObj.host}/${file.key}`,
                    name: file.name,
                    status: 2,
                    percent: 0,
                    key: file.key
                  })
                }
                if (hasNewFile) {
                  up.start()
                }
              })
            } catch (error) {
              that.uplaodIng = false
              that.$Message.warning({
                content: `: ${error.message}`,
                duration: 5
              })
            }
          },
          //  setOption
          async BeforeUpload (up, file) {
            // const authObj = that.authObj
            let authObj = that.authObj
            // 10s
            if (authObj.expire - Date.now() / 1000 < 10) {
              uploadGetAuth({
                fileName: file.name,
                empNo: that.empNo,
                systemName: that.platform
              })
                .then((res) => {
                  that.authObj = res
                })
                .catch((error) => {
                  that.$Message.warning({
                    content: `${error.message}`,
                    duration: 5
                  })
                })
            }
            file.key =
              authObj.dir + dateFormat(Date.now(), 'HH:mm:ss') + '-' + file.name
            that.fileList.forEach((f) => {
              if (f.name === file.name) {
                f.path = `${authObj.host}/${file.key}`
              }
            })
            let newMultipartParams = {
              key: file.key,
              policy: authObj.policy,
              expire: authObj.expire,
              OSSAccessKeyId: authObj.accessid,
              success_action_status: '200', // 200,204
              // 'callback': res.callback, // 
              signature: authObj.signature
            }
            up.setOption({
              url: authObj.host,
              multipart_params: newMultipartParams
            })
          },
          //  
          UploadProgress: function (up, file) {
            let fileItem = that.fileList.filter((f) => f.name === file.name)[0]
            if (fileItem) {
              fileItem.percent = file.percent
            }
          },
          // 
          FileUploaded: function (up, file, info) {
            let fileItem = that.fileList.filter((f) => f.name === file.name)[0]
            if (fileItem) {
              fileItem.status = 5
              setTimeout(() => {
                fileItem.percent = 101 //  100
              }, 1000)

              that.$emit('on-file-uploaded', fileItem)
            }
          },
          // 
          UploadComplete (up, files) {
            that.uplaodIng = false
          },
          Error: function (up, err) {
            that.uplaodIng = false
            // 
            that.fileList.forEach((f) => {
              if (f.name === err.file.name) {
                f.status = 4
              }
            })
            // 
            that.$Message.warning({
              content: ': ' + err.message,
              duration: 5
            })
          }
        }
      }))
      uploader.init()
    },
    //   
    initUploadFile () {
      const that = this
      let uploader = (this.uploader = new plupload.Uploader({
        runtimes: 'html5,html4',
        browse_button: that.btnId,
        multi_selection: that.multiSelection, // 
        url: `${runEnv() === 'prod' ? 'https' : 'http'}://oss.aliyuncs.com`,
        filters: {
          mime_types: [
            // zip
            { title: 'extensions supprot', extensions: that.extensions }
          ],
          max_file_size: that.maxFileSize, // 5mb
          prevent_duplicates: false // 
        },
        init: {
          PostInit: function () {
            // Init
          },
          FilesAdded: async function (up, files) {
            if (files.length > that.multiLength) {
              that.$Message.warning({
                content: `${that.multiLength}`,
                duration: 5
              })
              return
            }
            const empNo = JSON.parse(Cookie.get('columbusUserInfo')).id

            try {
              const authObj = await uploadGetAuth({
                fileName: files[0].name,
                empNo
              })
              that.authObj = authObj
              let hasNewFile = false
              files.forEach((file) => {
                let flag =
                  that.fileList.filter((f) => f.name === file.name).length > 0
                if (flag) {
                  that.$Message.warning({
                    content: `${file.name}`,
                    duration: 5
                  })
                  up.removeFile(file)
                } else {
                  hasNewFile = true
                  let key =
                    authObj.dir + that.guid() + '.' + file.name.split('.')[1]
                  file.key = key
                  that.fileList.push({
                    path: `${authObj.host}/${key}`,
                    name: file.name,
                    status: 2,
                    percent: 0,
                    key: file.key
                  })
                }

                if (hasNewFile) {
                  up.start()
                }
              })
            } catch (error) {
              that.$Message.warning({
                content: '',
                duration: 5
              })
            }
          },
          //  setOption
          async BeforeUpload (up, file) {
            // const authObj = that.authObj
            let authObj = that.authObj
            // 10s
            if (authObj.expire - Date.now() / 1000 < 10) {
              const empNo = JSON.parse(Cookie.get('columbusUserInfo')).id
              uploadGetAuth({ fileName: file.name, empNo })
                .then((res) => {
                  that.authObj = res
                })
                .catch((error) => {
                  that.$Message.warning({
                    content: `${error.message}`,
                    duration: 5
                  })
                })
            }

            file.key = authObj.dir + that.guid() + '.' + file.name.split('.')[1]

            that.fileList.forEach((f) => {
              if (f.name === file.name) {
                f.path = `${authObj.host}/${file.key}`
              }
            })

            let newMultipartParams = {
              key: file.key,
              policy: authObj.policy,
              expire: authObj.expire,
              OSSAccessKeyId: authObj.accessid,
              success_action_status: '200', // 200,204
              // 'callback': res.callback, // 
              signature: authObj.signature
            }
            up.setOption({
              url: authObj.host,
              multipart_params: newMultipartParams
            })
          },
          // 
          UploadProgress: function (up, file) {
            let fileItem = that.fileList.filter((f) => f.name === file.name)[0]
            if (fileItem) {
              fileItem.percent = file.percent
            }
          },
          // 
          FileUploaded: function (up, file, info) {
            let fileItem = that.fileList.filter((f) => f.name === file.name)[0]
            if (fileItem) {
              fileItem.status = 'finished'
              that.$emit('on-change', that.fileList)
            }
          },
          Error: function (up, err, ...rest) {
            console.log('err', err)
            that.fileList = that.fileList.filter(
              (f) => f.name !== err.file.name
            )
            // 
            if (err.code === -600) {
              that.$Message.warning({
                content: '' + that.maxFileSize,
                duration: 5
              })
            } else if (err.code === -601) {
              that.$Message.warning({
                content: '' + ExtensionsSupport,
                duration: 5
              })
            } else if (err.code === -602) {
              that.$Message.warning({
                content: '',
                duration: 5
              })
            } else {
              that.$Message.warning({
                content: '',
                duration: 5
              })
            }
          }
        }
      }))
      uploader.init()
    },
    handleClick (e) {
      this.$Message.warning({
        content: `${this.maxLength}`,
        duration: 10
      })
    }
  }
}
</script>

