<template>
  <Modal :mask-closable="false" title="" width="960" v-model="modalValue" :footer-hide="true">
    <Spin v-if="isLoading" fix></Spin>
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="120"
      label-position="left"
      v-if="modalValue"
    >
      <FormItem label="" prop="classId">
        <Select
          class="wd260"
          v-model="formValidate.classId"
          :disabled="operate === 3"
          placeholder=""
        >
          <Option
            :value="item.classId"
            v-for="(item, index) in classList"
            :key="index"
          >{{ item.className }}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="refundLesson" v-if="formValidate.classId">
        <CheckboxGroup v-model="formValidate.refundLesson">
          <div
            style="display: inline-block;"
            v-for="(item, index) in curClassInfo.curriculumList"
            :key="index"
          >
            <!--  -->
            <Tooltip :content="item.refundStatusName" v-if="item.refundStatus !== 1">
              <Checkbox
                :label="item.curriculumNo"
                :disabled="item.refundStatus !== 1 || operate === 3"
                class="border"
              ></Checkbox>
            </Tooltip>
            <Checkbox
              v-else
              :label="item.curriculumNo"
              :disabled="operate === 3"
              size="small"
              class="border"
            ></Checkbox>
          </div>
        </CheckboxGroup>
      </FormItem>
      <FormItem label=":" v-if="formValidate.classId">
        {{ curClassInfo.className }}
        <Button type="primary" size="small" ghost @click="checkAttendance"></Button>
      </FormItem>
      <Row
        v-if="formValidate.classId"
        type="flex"
        style="margin: 20px 0;font-size: 18px;font-weight: weight;"
      >
        <Col span="12">
          
          <span style="color: red;">{{ formValidate.refundMoney }}</span>&nbsp;&nbsp;&nbsp;&nbsp;
          
          <span
            style="color: red;"
          >{{ formValidate.currency || '--'}}</span>&nbsp;&nbsp;&nbsp;&nbsp;
        </Col>
        <Col
          span="12"
          v-if="$haveAuthority('roleRelativeData', 'apply/specialRefundApply/refundableAmount')"
        >
          <span style="float:left;"></span>
          <FormItem style="margin-bottom: 0;" label prop="actualRefundMoney">
            <Input
              style="width: 100px;"
              v-model="formValidate.actualRefundMoney"
              :disabled="operate === 3"
              number
              type="number"
              min="0"
            />
          </FormItem>
        </Col>
      </Row>
      <FormItem
        label=""
        prop="modifyMoneyReason"
        v-if="formValidate.refundMoney !== formValidate.actualRefundMoney"
      >
        <Input
          v-model="formValidate.modifyMoneyReason"
          :disabled="operate === 3"
          type="textarea"
          placeholder=""
        />
      </FormItem>
      <FormItem label="" prop="refundReason">
        <Input v-model="formValidate.refundReason" type="textarea" placeholder="" />
      </FormItem>
      <section class="ivu-form-item-required">
        <span class="ivu-form-item-label" style="display: inline-block;width: 120px;"></span>
        <div style="padding-left: 120px;margin: 30px 0;">
          <Tooltip max-width="500" placement="right">
            <span style="color: red;font-size: 18px;">
              
              <Icon type="ios-help-circle" />
            </span>
            <div slot="content">
              <div>
                <span>:</span>
                <p>1.</p>
                <p>2./</p>
              </div>
              <br />
              <div>
                <span>:</span>
                <p>1./</p>
                <p>2./</p>
                <p>3.</p>
                <p>4./</p>
              </div>
            </div>
          </Tooltip>
          <Fileupload
            v-model="formValidate.billCommonFileVos"
            multiSelection
            @on-change="handleFileChange"
          />
          <span
            v-if="!formValidate.billCommonFileVos.length && isShowFileWarn"
            style="color: red;"
          ></span>
        </div>
      </section>
      <FormItem label="" prop="refundType">
        <Select
          class="wd260"
          :disabled="operate === 3"
          v-model="formValidate.refundType"
          placeholder=""
        >
          <Option :value="0" v-if="refundType === 0"></Option>
          <Option :value="1" v-if="refundType == 1 || (refundType == 0 && $haveAuthority('roleRelativeData', 'apply/specialRefundApply/refundableAmount'))"></Option>
        </Select>
      </FormItem>
      <Row>
        <Col span="12">
          <FormItem label="" prop="serviceCenterDirector">
            <Select
              class="wd260"
              v-if="operate !== 3"
              v-model="formValidate.serviceCenterDirector"
              label-in-value
              filterable
              remote
              clearable
              :remote-method="q => selectRemoteLoad(q, 'servcieCenter')"
              :loading="servcieCenter.isLoading"
              placeholder=""
              @on-change="handleSelectChange($event, 'servcieCenter')"
            >
              <Option
                :value="item.empNo"
                v-for="(item, index) in servcieCenter.options"
                :key="index"
              >{{ item.name }} - {{item.empNo}}</Option>
            </Select>
            <Input
              class="wd260"
              v-else
              disabled
              :value="formValidate.serviceCenterDirectorName + '-' + formValidate.serviceCenterDirector"
            />
          </FormItem>
        </Col>
        <Col span="12">
          <FormItem label="" prop="regionalDirector">
            <Select
              class="wd260"
              v-if="operate !== 3"
              v-model="formValidate.regionalDirector"
              label-in-value
              filterable
              remote
              clearable
              :remote-method="q => selectRemoteLoad(q, 'district')"
              :loading="district.isLoading"
              placeholder=""
              @on-change="handleSelectChange($event, 'district')"
            >
              <Option
                :value="item.empNo"
                v-for="(item, index) in district.options"
                :key="index"
              >{{ item.name }} - {{item.empNo}}</Option>
            </Select>
            <Input
              class="wd260"
              v-else
              disabled
              :value="formValidate.regionalDirectorName + '-' + formValidate.regionalDirector"
            />
          </FormItem>
        </Col>
      </Row>
      <!--  -->
      <section v-if="formValidate.refundType == 1">
        <p></p>
        <Tooltip max-width="500" placement="right" style="margin-left: 5%;margin-top: 20px;">
          <span style="color: red;font-size: 18px;">
            
            <Icon type="ios-help-circle" />
          </span>
          <div slot="content">
            <p>1.+</p>
            <p>2.</p>
            <p>3.</p>
            <p>4.</p>
            <p>5.</p>
          </div>
        </Tooltip>
        <div style="width: 90%;margin: 20px auto;">
          <BankinfoCollect ref="bankinfoCollect" v-model="formValidate.onlineBank" />
        </div>
      </section>
      <!--  -->
      <Row type="flex" justify="center">
        <Col>
          <Button :loading="submitDisable" type="primary" @click="handleSubmit('formValidate')"></Button>
        </Col>
        <Col>
          <Button style="margin-left: 30px" @click="modalValue=false;"></Button>
        </Col>
      </Row>
    </Form>
    <Modal
      :mask-closable="false"
      class="has-tag"
      v-model="isShowAttendanceModal"
      width="900"
      :footer-hide="true"
      title=""
    >
      <TalStuAttendance
        :classId="curClassInfo.classId"
        :studentDetail="studentDetail"
        v-if="isShowAttendanceModal"
      ></TalStuAttendance>
    </Modal>
  </Modal>
</template>

<script>
import { getSpecialRefundCurriculumList, getWorkOrder, calculateSpecialRefundAmount, getEmployeeList, submit, checkRefundAmount } from '../../api/specialRefund'
import BankinfoCollect from './bankinfo-collect'
import Fileupload from './fileupload'
import Cookie from 'js-cookie'

export default {
  name: 'TalFinanceRefundSpecialApply',
  components: {
    BankinfoCollect,
    Fileupload
  },
  props: {
    show: {
      type: Boolean,
      required: true,
      default: false
    },
    //  2  3
    applyAgain: {
      type: Number,
      default: 1
    },
    // id
    workOrderId: {
      type: String,
      default: ''
    },
    // code
    cityCode: {
      type: String,
      default: '',
      required: true
    },
    // code
    areaCode: {
      type: String,
      default: ''
    },
    // uid
    studentUid: {
      type: [String, Number],
      required: true,
      default: ''
    },
    // id
    studentId: {
      type: String,
      default: ''
    },
    // 
    studentName: {
      type: String,
      default: ''
    },
    // student card
    studentCard: {
      type: String,
      default: ''
    },
    // id
    registerId: {
      type: String,
      required: true,
      default: ''
    },

    // 
    stageName: {
      type: String,
      default: ''
    },
    stageId: {
      type: String,
      default: ''
    },
    // 
    classStagesNo: {
      type: Number,
      required: true
    },
    // 
    stagesCurriculumCount: {
      type: [String, Number],
      default: 1
    },
    // 
    paidAmount: {
      type: Number,
      default: 0
    },
    // 
    refundNo: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      formValidate: {
        classId: '', // 
        refundLesson: [], // 
        refundReason: '', // 
        refundType: '', // 
        billCommonFileVos: [], // 
        serviceCenterDirector: '', // 
        regionalDirector: '', // 
        onlineBank: {
          payName: '', // 
          payBankAccount: '', // 
          payBankType: '', // 
          payBankTypeName: '', // 
          payBankProvince: '', // 
          payBankProvinceName: '', // 
          payBankCity: '', // 
          payBankCityName: '', // 
          payBankName: '' // 
        },
        refundMoney: 0, // 
        actualRefundMoney: 0,
        modifyMoneyReason: '',
        currency: '' // 
      },
      ruleValidate: {
        classId: [{ required: true, message: '', trigger: 'change', type: 'string' }],
        refundLesson: [{ required: true, message: '', trigger: 'change', min: 1, type: 'array' }],
        refundReason: [
          { required: true, message: '', trigger: 'blur', type: 'string' },
          { max: 200, message: '', trigger: 'blur', type: 'string' }
        ],
        refundType: [{ required: true, message: '', trigger: 'change', type: 'number' }],
        serviceCenterDirector: [{ required: true, message: '', trigger: 'change', type: 'string' }],
        regionalDirector: [{ required: true, message: '', trigger: 'change', type: 'string' }],
        actualRefundMoney: [
          { required: true, message: '' },
          { min: 0, type: 'integer', message: '0' }
        ],
        modifyMoneyReason: [
          { required: true, message: '', trigger: 'blur', type: 'string' },
          { max: 200, message: '', trigger: 'blur', type: 'string' }
        ]
      },
      isShowAttendanceModal: false, // 
      // 
      servcieCenter: {
        isLoading: false,
        options: []
      },
      // 
      district: {
        isLoading: false,
        options: []
      },
      // 
      classList: [],
      refundType: 0, //  0  1 
      isRole: false, // true   false 
      isLoading: true, // ...
      operate: 1, //  123
      submitDisable: false, // 
      isShowFileWarn: false // 
    }
  },
  computed: {
    studentDetail () {
      return { cityCode: this.cityCode, studentId: this.studentId }
    },
    // 
    curClassInfo () {
      return this.getCurClassInfoByclassId(this.formValidate.classId)
    },
    modalValue: {
      get: function () {
        if (this.show) {
          this.$nextTick(() => {
            //  
            this.$refs.formValidate.resetFields()
            const { ruleValidate, ...rest } = this.$options.data()
            // form data  rules
            Object.assign(this.$data, rest)
          })
        }
        return this.show
      },
      set: function (val) {
        this.$emit('close', val)
      }
    }
  },
  methods: {
    // id
    getCurClassInfoByclassId (classId) {
      if (!classId) return []
      let curClassInfo = []
      for (let obj of this.classList) {
        if (obj.classId === classId) {
          curClassInfo = { ...obj }
          break
        }
      }

      return curClassInfo
    },
    async selectRemoteLoad (query, type) {
      this[type].isLoading = true
      try {
        const params = {
          cityCode: this.cityCode,
          name: isNaN(Number(query)) ? query : '',
          empNo: isNaN(Number(query)) ? '' : query
        }
        const data = await getEmployeeList(params)

        this[type].isLoading = false
        this[type].options = Object.freeze(data)
      } catch (error) {
        this.$Message.error({ content: ':' + error.message, duration: 5 })
      }
    },
    handleSelectChange (data, type) {
      if (!data) return
      const name = data.label.split(' - ')[0]
      if (type === 'district') {
        this.formValidate.regionalDirectorName = name
      } else {
        this.formValidate.serviceCenterDirectorName = name
      }
    },
    // 
    handleFileChange () {
      this.isShowFileWarn = true
    },
    // 
    async handleSubmit (name) {
      let params = Object.assign({}, this.formValidate, {
        applyEmpName: JSON.parse(Cookie.get('columbusUserInfo')).name,
        cityCode: this.formValidate.cityCode || this.cityCode,
        areaCode: this.formValidate.cityCode || this.cityCode,

        operate: this.operate,
        refundLesson: this.formValidate.refundLesson.join(','),
        refundMoney: this.formValidate.refundMoney.toFixed(2),
        registerId: this.formValidate.registerId || this.registerId,

        studentCard: this.formValidate.studentCard || this.studentCard,
        studentId: this.formValidate.studentId || this.studentId,
        studentName: this.formValidate.studentName || this.studentName,
        studentUid: this.formValidate.studentUid || this.studentUid,

        stageName: this.formValidate.stageName || this.stageName,
        stageId: this.formValidate.stageId || this.stageId,
        stageNum: this.formValidate.stageNum || this.classStagesNo,
        stagesCurriculumCount: this.formValidate.stagesCurriculumCount || this.stagesCurriculumCount,
        totalMoney: this.formValidate.totalMoney || this.paidAmount,
        billCommonFileVos: this.formValidate.billCommonFileVos.filter(item => item.status === 'finished')
      })

      if (params.billCommonFileVos.length < this.formValidate.billCommonFileVos.length) {
        return this.$Message.warning({ content: '', duration: 5 })
      }

      // console.log('---- submit params -----', params)

      if (this.formValidate.refundType === 1) {
        params = Object.assign(params, { ...this.formValidate.onlineBank })
      }

      try {
        let valid1 = true; let valid2 = true; let valid3 = this.formValidate.billCommonFileVos.length
        valid1 = await this.$refs[name].validate()
        this.isShowFileWarn = true

        if (this.formValidate.refundType === 1) {
          valid2 = await this.$refs.bankinfoCollect.validate()
        }

        if (valid1 && valid2 && valid3) {
          this.submitDisable = true

          // 
          const data = await checkRefundAmount({
            refundAmount: params.actualRefundMoney || Number(params.refundMoney),
            registerId: params.registerId,
            stagesNum: params.stageNum,
            cityCode: params.cityCode,
            refundNo: this.refundNo
          })

          // 
          if (data.isOverRun) {
            this.submitDisable = false
            return this.$Message.warning({
              content: `,${data.maxAmount}`
            })
          }

          await submit(params)
          setTimeout(() => {
            this.modalValue = false
            this.$Message.success('')
            this.$emit('success')
            this.submitDisable = false
          }, 2000)
        }
      } catch (error) {
        this.submitDisable = false
        this.$Message.error({ content: ':' + error.message, duration: 5 })
      }
    },
    // 
    checkAttendance () {
      this.isShowAttendanceModal = true
    },
    // 
    async getClassList () {
      try {
        const params = {
          classStagesNo: this.classStagesNo, //
          studentUid: this.studentUid,
          registerId: this.registerId,
          cityCode: this.cityCode
        }
        const data = await getSpecialRefundCurriculumList(params)
        this.classList = Object.freeze(data.classList) || []
        this.refundType = data.refundType
        this.isRole = data.isRole
        this.formValidate.refundType = data.refundType
        this.formValidate.currency = data.currency
      } catch (error) {
        this.$Message.error({ content: ':' + error.message, duration: 5 })
      }
    },
    // 
    async getWorkOrder () {
      const params = {
        cityCode: this.cityCode,
        workOrderId: this.workOrderId
      }
      try {
        const data = await getWorkOrder(params)
        const refundLesson = data.refundLesson.split(',').map(s => Number(s))
        const billCommonFileVos = data.billCommonFileVos.map(item => Object.assign(item, { percent: 100, status: 'finished' }))

        // const onlineBank = {
        //   payName: data.payName,
        //   payBankAccount: data.payBankAccount,
        //   payBankName: data.payBankName,
        //   payBankType: data.payBankType,
        //   payBankTypeName: data.payBankTypeName,
        //   payBankProvince: data.payBankProvince,
        //   payBankProvinceName: data.payBankProvinceName,
        //   payBankCity: data.payBankCity,
        //   payBankCityName: data.payBankCityName
        // }

        this.formValidate = Object.assign(this.formValidate, { ...data, refundLesson, billCommonFileVos })
        // this.formValidate.refundType = 1;

        if (this.applyAgain === 3) {
          this.formValidate.refundType = 1
          this.refundType = 1
        }

        if (this.formValidate.actualRefundMoney === null) {
          this.formValidate.actualRefundMoney = this.formValidate.refundMoney
        }

        this.operate = this.applyAgain

        // 
        this.classList.forEach(item => {
          if (item.classId === data.classId) {
            item.curriculumList.forEach(_item => {
              _item.refundStatus = data.refundLesson.indexOf(_item.curriculumNo) === -1 ? _item.refundStatus : 1
            })
          }
        })
      } catch (error) {
        this.$Message.error({ content: ':' + error.message, duration: 5 })
      }
    },
    // 
    async calculateSpecialRefundAmount () {
      if (this.formValidate.refundLesson.length === 0) {
        this.formValidate.refundMoney = 0
        this.formValidate.actualRefundMoney = 0
        return
      }
      const req = {
        classStagesNo: this.classStagesNo,
        curriculumNoList: this.formValidate.refundLesson,
        cityCode: this.cityCode,
        registerId: this.registerId
      }
      try {
        const data = await calculateSpecialRefundAmount(req)
        this.formValidate.refundMoney = this.formValidate.actualRefundMoney = data.refundAmount
      } catch (error) {
        this.$Message.error({ content: ':' + error.message, duration: 5 })
        this.formValidate.refundLesson.splice(-1)
      }
    }
  },
  watch: {
    async show (n) {
      if (n) {
        this.isLoading = true
        await this.getClassList()
        // 
        if (this.workOrderId) {
          await this.getWorkOrder()
        }
        setTimeout(() => {
          this.isLoading = false
        }, 200)
      }
    },
    'formValidate.refundLesson': {
      handler () {
        if (this.isLoading) return
        this.calculateSpecialRefundAmount()
      }
    },
    // 
    'formValidate.classId': {
      handler () {
        if (this.isLoading) return
        this.formValidate.refundLesson = []
        this.formValidate.payMoney = 0
      }
    }
  }
}
</script>

<style lang="less" scoped>
.wd260 {
  width: 260px;
}

.dot {
  /deep/ .ivu-tag-dot-inner {
    width: 7px;
    height: 7px;
    top: -1px;
  }
}
.ivu-tag-dot {
  border: none !important;
}
.ivu-tag-dot-inner {
  display: inline-block;
  width: 7px;
  height: 7px;
  margin-right: 8px;
  border-radius: 50%;
  background: #e8eaec;
  position: relative;
  top: -1px;
}

.border {
  border: 1px solid #dcdee2;
  border-radius: 4px;
  height: 32px;
  line-height: 30px;
  padding: 0 15px;
  transition: border 0.2s ease-in-out;
}

.ivu-checkbox-wrapper-checked.border {
  border-color: #2d8cf0;
}
</style>
