<template>
  <div class="order">
    <div class="no-data" v-if="orderList.length === 0"></div>
    <Timeline v-else style="margin-top: 10px">
      <TimelineItem v-for="(order, index) in orderList" :key="index">
        <p class="time">{{order.title}}<span>[{{String(order.advisorType) === '5' ? '' : String(order.advisorType) === '8' ? '': ''}}]</span></p>
        <div class="tags">
          <span></span>
          <Tag class="tag" color="#83BEFF" v-for="(tag, index) in order.studentTagList" :key="index">{{tag.personalityTag}}</Tag>
        </div>
        <div class="content">
          <Collapse simple>
            <Panel name="1">
              <a></a>
              <div slot="content">
                <p><span class="bold-text"></span>{{order.contactResult || ''}}</p>
                <div style="margin-top: 10px" v-if="order.userInfo">
                  <span class="bold-text">{{order.userInfo.readStatusName || ''}} | </span>
                  <span class="bold-text">{{order.userInfo.stuGradeName || ''}} | </span>
                  <span class="bold-text">{{order.userInfo.stuSex ? '' : ''}} | </span>
                  <span class="bold-text">{{order.userInfo.cityName || ''}} | </span>
                  <span class="bold-text">{{order.userInfo.belongCityName || ''}} | </span>
                  <span class="bold-text">{{order.userInfo.serviceCenterName || ''}} | </span>
                  <span class="bold-text">{{order.userInfo.consultant || ''}}</span>
                  <span v-if="order.userInfo.title" class="bold-text"> | {{order.userInfo.title || ''}}</span>
                  <template v-if="order.userInfo.diagnosisUrl">
                    <span class="bold-text"> | <a v-if='order.userInfo.diagnosisUrl' :href='order.userInfo.diagnosisUrl' target='_blank'>{{order.userInfo.cityName }}{{ order.userInfo.stuGradeName }}</a> | </span>
                    <span>{{order.userInfo.gradeName || ''}} | </span>
                    <span>{{order.userInfo.subjectName  || ''}} | </span>
                    <span>{{order.userInfo.score  || ''}} | </span>
                    <span>{{order.userInfo.levelDegreeName || ''}} | </span>
                    <span>{{order.userInfo.levelName || ''}} | </span>
                    <span>{{order.userInfo.sourceTypeName || ''}} | </span>
                    <span> | </span>
                    <span>{{order.userInfo.answerTime|dateFilter}}</span>
                  </template>
                </div>
              </div>
            </Panel>
          </Collapse>
        </div>
      </TimelineItem>
      <TimelineItem>
        <a v-if="showMore" @click="more">...</a>
        <span v-else></span>
      </TimelineItem>
    </Timeline>
  </div>
</template>
<script>
  import { dateFormat } from '../../utils/utils'
  import { getWorkOrderHistory } from '../../api/adviser'
  export default {
    name: 'TalOnlineAdviserRecord',
    data () {
      return {
        orderList: [],
        showMore: true,
        pageNo: 1
      }
    },
    props: {
      cityCode: {
        type: String,
        default: ''
      },
      uid: {
        type: [String, Number],
        default: ''
      }
    },
    filters: {
      dateFilter (val) {
        return dateFormat(val)
      }
    },
    methods: {
      refresh () {
        this.orderList = []
        this.showMore = true
        this.pageNo = 1
        this.queryOrder()
      },
      more () {
        this.pageNo += 1
        this.queryOrder()
      },
      queryOrder () {
        getWorkOrderHistory({
          cityCode: this.cityCode,
          pyUid: this.uid,
          pageNo: this.pageNo
        }).then(data => {
          if (data) {
            this.orderList = this.orderList.concat(data.map(d => {
              if (d.studentJson) {
                d.userInfo = JSON.parse(d.studentJson)
              }
              return d
            }))
            if (this.pageNo > 1 && data.length < 5) {
              this.showMore = false
            }
          } else {
            this.orderList = []
          }
        })
      }
    },
    watch: {
      uid () {
        if (this.uid && this.cityCode) {
          this.refresh()
        }
      }
    }
  }
</script>
<style scoped lang="less">
  .order {
    overflow-y: auto;
  }
  .no-data {
    margin-top: 10px;
  }
  .time{
    font-size: 14px;
    font-weight: bold;
    span {
      margin-left: 5px;
      color: #2d8cf0;
      font-weight: normal;
    }
  }
  .tags {
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
    margin: 10px 0;
    .tag {
      border-radius: 30px;
    }
  }
  .bold-text {
    font-weight: bold;
  }

</style>
