import $ from '../utils/api.request'

/**
 * 
 * @param params
 * @returns {*}
 */
export const findMessageSendRecords = params => {
  return $.splatAxios.request({
    url: 'messageSendInfo/findMessageSendRecords',
    params: params,
    method: 'get'
  })
}
/**
 * 
 * @param params
 * @returns {*}
 */
export const sendPaymentVoucher = params => {
  return $.splatAxios.request({
    url: 'message/sendPaymentVoucher',
    data: params,
    method: 'post'
  })
}

