<!-- OMO -->

<template>
    <Modal
      :mask-closable="false"
      title=""
      v-model="modalValue"
      width="560px">
        <p class="warn">OMO</p>
        <div>
            <p>: {{ sourseClassDetail.name }}</p>
            <p>: {{ sourseClassDetail.price}}{{ sourseClassDetail.feetype === 1 ? '' : ''}}</p>
            <p>: {{ classType[sourseClassDetail.classType]}}</p>
            <p v-if="promotionArray.length > 0 || couponArray.length">: </p>
            <p class="activity" v-for="(item, index) in promotionArray" :key="index">-{{ item.promotionName }}{{ item.promotionActualPrice }}</p>
            <p class="activity" v-for="(item, index) in couponArray" :key="index">-{{ item.couponActualValue }}</p>
        </div>
        <Divider />
        <div>
            <p>: {{ targetClassDetail.name }}</p>
            <p>: {{ targetClassDetail.price}}{{ sourseClassDetail.feetype === 1 ? '' : ''}}</p>
            <p>: {{ classType[targetClassDetail.classType]}}</p>
        </div>
        <div slot="footer">
            <Button @click="confirmChangeClass" type="primary"></Button>
            <Button @click="modalValue=false"></Button>
        </div>
    </Modal>
</template>

<script>
import { getOMOChangeClass } from '../../api/register'
export default {
  name: 'TalOmoChangeclassRiskwarningModal',
  data () {
    return {
      modalValue: false,
      params: {
        registId: '', // ID--
        sourceClassId: '', // id --
        targetClassId: '', // id
        operationType: '', // 0: 1: 7: 8: 9:
        targetNum: null, // 
        studentUid: '' // UID --
      },
      sourseClassDetail: {},
      targetClassDetail: {},
      promotionArray: [],
      couponArray: [], // 
      classType: {
        1: '',
        2: '',
        4: '',
        8: ''
      }
    }
  },
  methods: {
    confirmChangeClass () {
      this.modalValue = false
      this.$emit('confirm-change-class', this.params)
    },
    getOMOChangeClass (params) {
      // params = {
      //   registId: '683f3aa2918b4c17aae8121e8d56d24d',
      //   sourceClassId: '55dbf91c78f3442798b64415f93fac90',
      //   targetClassId: '84e362ca0d4f4dc2a7989b7b890211d7',
      //   operationType: '0',
      //   targetNum: 1,
      //   studentUid: '851727638'
      // }

      console.log('params', params)

      this.params = params

      return getOMOChangeClass(params).then(res => {
        // omo
        if (res.validateOmo) {
          this.modalValue = true
          const { sourseClassDetail, targetClassDetail, promotionArray, couponArray } = res
          this.sourseClassDetail = sourseClassDetail
          this.targetClassDetail = targetClassDetail
          this.promotionArray = promotionArray || []
          this.couponArray = couponArray || []
        } else {
          return true
        }
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.warn {
  color: red;
  font-size: 16px;
}

.activity {
  padding-left: 50px;
}
</style>
