<!---->
<template>
  <div class="changeInfo">
    <Spin fix v-if="loading">
      <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
      <div></div>
    </Spin>
    <Row>
      <Col span="2" style="border-right: 1px solid gainsboro">
        <div class="table-tag" :class="tabContent === 'changeList'? 'tabColor' : 'changeBtn'"  @click="changeTable('changeList')"></div>
        <div class="table-tag" :class="tabContent === 'preChangeList'? 'tabColor' : 'changeBtn'"  @click="changeTable('preChangeList')"></div>
      </Col>
      <Col span="22" style="padding: 10px">
        <div class="newTab">
          <Table stripe :columns="tableColumns" :data="tableData" height="500"></Table>
        </div>
      </Col>
    </Row>
  </div>
</template>

<script>
import {
  queryClassChangeInfo,
  queryClassPrechangeInfo
} from '../../../api/clazz'
export default {
  name: 'changeInfo',
  props: {
    classInfo: {
      type: Object,
      require: true
    }
  },
  data () {
    return {
      loading: false,
      tabContent: '',
      tableColumns: [],
      tableData: [],
      changeListColumns: [ // 
        {
          title: '',
          key: 'index',
          align: 'center'
        },
        {
          title: '',
          key: 'totalClassNum',
          align: 'center'
        },
        {
          title: '',
          key: 'classTeacherName',
          align: 'center',
          render: (h, params) => {
            return h('span', this.getChangetype(params.row.changeType))
          }
        },
        {
          title: '',
          key: 'classTeacherType', // 
          align: 'center'
        },
        {
          title: '',
          key: 'msmType',
          align: 'center',
          render: (h, params) => {
            return h('span', this.getAdvice(params.row.msmType))
          }
        },
        {
          title: '',
          key: 'changeContent',
          align: 'center',
          render: (h, params) => {
            return h('Poptip', {
              style: {
                display: 'inline-block',
                marginRight: '5px'
              },
              props: {
                'popper-class': 'tal-popup',
                trigger: 'hover',
                width: '300',
                wordWrap: true,
                placement: 'left',
                content: params.row.changeContent
              }
            }, this.getClassChangeName(params.row.changeContent))
          }
        },
        {
          title: '',
          key: 'changeReason',
          align: 'center'
        },
        {
          title: '',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '',
          key: 'createrName',
          align: 'center'
        }
      ],
      preChangeListColumns: [
        {
          title: '',
          key: 'index',
          align: 'center',
          width: 70
        },
        {
          title: '',
          key: 'classNum',
          align: 'center',
          width: 100
        },
        {
          title: '',
          key: 'preInfo',
          align: 'center'
        },
        {
          title: '',
          key: 'changeProgress',
          align: 'center'
        },
        {
          title: '',
          key: 'classChangeAuditDate',
          align: 'center'
        },
        {
          title: '',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '',
          key: 'createName',
          align: 'center',
          width: 120
        }
      ],
      advice: {
        1: '',
        2: '',
        3: '',
        4: '',
        5: ''
      },
      changeType: {
        0: '',
        1: '',
        2: ''
      }
    }
  },
  methods: {
    initView () {
      this.changeTable('changeList')
    },
    changeTable (a) {
      this.tableData = []
      this.tabContent = a
      switch (a) {
        case 'changeList':
          this.changeListHandler()
          break
        case 'preChangeList':
          this.preChangeListHandler()
          break
      }
    },
    changeListHandler () {
      this.loading = true
      this.tableData = []
      this.tableColumns = this.changeListColumns
      let changeData = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id
      }
      queryClassChangeInfo(changeData).then((res) => {
        if (res.length <= 0) {
          this.tableData = res
        } else {
          this.tableData = res
          this.tableData.forEach((e, index) => {
            e.index = index + 1
          })
        }
      }).catch(err => {
        // this.$Message.error({ content: err, duration: 5 })
        this.$emit('errMsg', ':' + err)
      }).finally(() => {
        this.loading = false
      })
    },
    preChangeListHandler () {
      this.loading = true
      this.tableData = []
      let req = {
        area: this.classInfo.cityId,
        classId: this.classInfo.id
      }
      this.tableColumns = this.preChangeListColumns
      queryClassPrechangeInfo(req).then(res => {
        if (res) {
          this.buildPreChangeData(res)
        }
      }).catch(err => {
        // this.$Message.error({ content: err, duration: 5 })
        this.$emit('errMsg', ':' + err)
      }).finally(() => {
        this.loading = false
      })
    },
    buildPreChangeData (data) {
      let res = []
      data.forEach((obj, idx) => {
        // 	1
        let index = idx + 1
        // 
        let classNum = obj.callTask.startClassNum + (obj.callTask.endClassNum !== obj.callTask.startClassNum ? `-${obj.callTask.endClassNum}` : '')
        //      
        let preInfo = `${obj.callTask.startClassNum}-${obj.callTask.endClassNum}`
        obj.callChangeItemList.forEach(item => {
          preInfo += `${item.changeTypeName}${item.newName}`
        })
        /**  callTask.status 4   callPercentage callTask.changeProgress
         *
         * WAIT_AUDIT("1", ""),
         WAIT_PUSH("2", ""),
         AUDIT_REFUSE("3", ""),
         CALL("4", ""),
         PUSH_REFUSE("5", ""),
         WAIT_TEA_CONFIRM("6", ""),
         WAIT_CHANGE("7", ""),
         TASK_DONE("8", "");
         * @type {string}
         */
        let changeProgress = ''
        if (Number(obj.callTask.status) === 4) {
          changeProgress = `${obj.callTask.changeProgress}${obj.callPercentage}%`
        } else {
          changeProgress = obj.callTask.changeProgress
        }
        // 
        let createDate = obj.callTask.createDate || '--'
        // 
        let classChangeAuditDate = obj.callTask.classChangeAuditDate || '--'
        // 
        let createName = obj.callTask.createName || '--'
        res.push({
          index,
          classNum,
          preInfo,
          changeProgress,
          createDate,
          classChangeAuditDate,
          createName
        })
      })
      this.tableData = res
    },
    getAdvice (val) {
      return this.advice[val]
    },
    getChangetype (val) {
      return this.changeType[val]
    },
    // 
    getClassChangeName (name) {
      if (name && name.length > 30) {
        return name.substring(0, 30) + '...'
      } else {
        return name
      }
    }
  }
}
</script>

<style lang="less">
  .changeInfo {
    position: relative;
    .table-tag {
      margin-top: 20px;
      text-align: center;
      cursor: pointer;
      padding: 3px;
      &.tabColor{
        color: #008CF8;
      }
    }
    .ivu-table-wrapper{
      overflow: unset;
    }
  }
  body /deep/ .tal-popup {
    z-index: 29999 !important;
  }
</style>
