<script>
import {
  getChangeClassType,
  changeClass,
  changeClassResult
} from '../../api/register'
import {
  getClassType,
  getLinkage,
  getAreaList as getArea,
  getVenueList as getVenue,
  getTeacherByNameFuzzy,
  getServiceCenterList as getServiceCenter
  // getClassTimeTypeList
} from '../../api/bd'
import {
  getClassInfo
} from '../../api/clazz'
import ClassMixin from '../../mixin/classDetail'
import classDetail from '../tal-class-details'
export default {
  mixins: [ ClassMixin ],
  components: { classDetail },
  name: 'TalChangeClass',
  props: {
    studentDetail: {
      type: Object,
      default: function () {
        return {}
      }
    },
    value: {
      type: Boolean,
      default: false
    },
    changeClassType: {
      type: Number,
      default: 0
    },
    oldClassInfo: {
      type: Object,
      default: function () {
        return {}
      }
    },
    from: { // ChangeCourseReadingClass
      type: String,
      default: ''
    }
  },
  created () {

  },
  data () {
    return {
      isLoading: false, // 
      modalValue2: false, // 
      allCityList: [],
      searchLoading: false, // loading
      oldSearchName: '', // 
      targetClassName: '', // 
      searchFormHidden: false,
      pageNum: 1,
      pageSize: 10,
      pageTotal: 0,
      classTypeList: [
        // { value: '1', label: '' },
        // { value: '2', label: '' },
        // { value: '4', label: '' }
      ],
      classTypeMap: {
        '1': '',
        '2': '',
        '4': ''
      },
      gradeTypeList: [], // 
      gradeList: [], // 
      levelList: [], // 
      areaList: [], // 
      serviceCenterList: [], // 
      venueList: [], // 
      classTimeTypeList: ['', '', '', '', '', '', '', '', '', '', '', '', '', ''], // 
      teacherList: [], // 
      tutorTeacherList: [], // 
      changeClassTypeList: [], // 
      startDate: '',
      endDate: '',
      // 
      formData1: {
        registId: '', // id
        studentId: '', // id
        targetClassId: '', // id
        sourceClassId: '', // id
        beginNum: null, // 
        cityCode: this.studentDetail.cityCode, // 
        description: '', // 
        changeClassType: this.changeClassType, //  0- 1-
        reasonTypeId: '', // id
        isSpecial: 0 //  0- 1-
      },
      // 
      formData2: {
        cityCode: '', // 	
        year: new Date().getFullYear() + '', // 	
        termId: '', // id	
        gradeTypeId: '', // id	
        gradeId: '',	// String	id
        levelId: '',		// String	id
        classType: '', // 
        teacherIds: '', // id
        tutorId: '', // id
        subjectIds: '', // id
        areaId: '', // 
        serviceCenterId: '', // id
        venueId: '', // id
        classtimeTypeId: '', // id
        classtimeTimePeriod: '', // 
        startDate: '', // start
        endDate: '', // end
        isSchoolTest: undefined, // 
        claStartCount: null, // start
        claEndCount: null, // end
        minRemainNum: null, // start
        maxRemainNum: null // end
      },
      tableData: [],
      tableColumns: [
        {
          title: '',
          key: 'name',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('div', {
                style: {
                  'text-align': 'right'
                }
              }, [
                h('Tag', {
                  style: {
                    'display': params.row.businessType === 1 ? 'inline-block' : 'none'
                  },
                  props: {
                    color: 'green'
                  }
                }, '')
              ]),
              h('a', {
                on: {
                  click: () => {
                    this.showClassDetail(params.row.id)
                  }
                }
              }, params.row.name)
            ])
          }
        },
        { title: '',
          key: 'classType',
          align: 'center',
          render: (h, params) => {
            return h('span', {
              style: {}

            }, this.getClassType(params.row.classType))
          } },
        { title: '', key: 'subjectNames', align: 'center' },
        { title: '', key: 'teacherNames', align: 'center' },
        { title: '', key: 'tutorRealName', align: 'center' },
        { title: '', key: 'startDate', align: 'center' },
        { title: '', key: 'endDate', align: 'center' },
        { title: '', key: 'classtimeNames', align: 'center' },
        { title: '', key: 'serviceCenterName', align: 'center' },
        { title: '', key: 'classroomName', align: 'center' },
        { title: '',
          key: 'classjin',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.passedCount + '/' + params.row.classCount)
          } },
        { title: '', key: 'baojin', align: 'center' },
        { title: '', key: 'remainNum', align: 'center' },
        { title: '', key: 'waitCount', align: 'center' },
        { title: '', key: 'price', align: 'center' },
        { title: '',
          key: 'classChangeName',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('Poptip', {
              style: {
                display: 'inline-block',
                marginRight: '5px'
              },
              props: {
                trigger: 'hover',
                width: '300',
                wordWrap: true,
                content: params.row.classChangeName
              }
            }, this.getClassChangeName(params.row.classChangeName))
          }
        },
        { title: '',
          key: 'isSchooltest',
          width: 130,
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.isSchooltest === 0 ? '' : '')
          } },
        {
          title: '',
          key: 'caozuo',
          width: 80,
          align: 'center',
          render: (h, params) => {
            if (params.row.id !== this.oldClassInfo.id) {
              return h('div', [
                h('a', {
                  style: {
                    display: 'inline-block',
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.changeConfirm(params.row)
                    }
                  }
                }, '')
              ])
            }
          }
        }
      ]
    }
  },
  methods: {
    getClassTypeHandler () {
      let req = {}
      getClassType(req).then(res => {
        this.classTypeList = res
        this.formData2.classType = this.oldClassInfo.classType
        this.getLevelList()
      }).catch(err => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    getClassChangeName (name) {
      if (name && name.length > 30) {
        return name.substring(0, 30) + '...'
      } else {
        return name
      }
    },
    save () {
      if (this.isLoading) {
        return
      }
      if (!this.formData1.beginNum) {
        this.$Message.warning({ content: '', duration: 5 })
        return
      }
      if (this.formData1.beginNum > this.oldClassInfo.classCount) {
        this.$Message.warning({ content: '', duration: 5 })
        return
      }
      if (!this.formData1.targetClassId) {
        this.$Message.warning({ content: '', duration: 5 })
        return
      }
      this.formData1.studentId = this.studentDetail.studentId
      this.formData1.sourceClassId = this.oldClassInfo.classId
      this.formData1.cityCode = this.studentDetail.cityCode // 
      this.formData1.uid = this.oldClassInfo.uid
      this.formData1.registId = this.oldClassInfo.registId
      this.formData1['X-Ca-referer'] = this.from
      this.isLoading = true
      changeClass(this.formData1).then(data => {
        const int = setInterval(() => {
          changeClassResult({
            cityCode: data.cityCode,
            studentId: this.studentDetail.studentId,
            classId: this.formData1.targetClassId, // id
            sourceClassId: this.oldClassInfo.classId, // id
            targetNum: this.formData1.beginNum, // 
            registId: data.registId,
            tradeResultId: data.queryKey,
            'X-Ca-referer': this.from
          }).then(rr => {
            if (rr.execResult !== '0') {
              clearInterval(int)
              if (rr.execResult === '1') {
                this.modalValue = false
                this.$Message.success({ content: '', duration: 5 })
                this.$emit('success')
              } else if (rr.execResult === '2') {
                this.$Message.error({ content: `${rr.message}`, duration: 5 })
                this.$emit('fail')
              }
              this.isLoading = false
            }
          }).catch(err => {
            this.isLoading = false
            clearInterval(int)
            this.$Message.error({ content: `${err.message}`, duration: 5 })
          })
        }, 1000)
      }).catch((err) => {
        this.$Message.error({ content: ':' + err.message, duration: 5 })
        this.isLoading = false
      })
    },

    // 
    startDateChange (val) {
      if (this.formData2.endDate) {
        if (new Date(val).getTime() > new Date(this.formData2.endDate).getTime()) {
          this.$Message.warning({ content: '', duration: 5 })
          this.formData2.startDate = ''
          this.startDate = ''
          return
        }
      }
      this.formData2.startDate = val
    },
    endDateChange (val) {
      if (this.formData2.startDate) {
        if (new Date(this.formData2.startDate).getTime() > new Date(val).getTime()) {
          this.$Message.warning({ content: '', duration: 5 })
          this.formData2.endDate = ''
          this.endDate = ''
          return
        }
      }
      this.formData2.endDate = val
    },

    // 
    areaChange (val) {
      this.serviceCenterList = []
      this.formData2.serviceCenterId = ''
      if (!val) {
        return false
      }
      this.getServiceCenter()
    },
    serviceChange (val) {
      this.venueList = []
      this.formData2.venueId = ''
      if (!val) {
        return false
      }
      this.getVenue()
    },
    // 
    getServiceCenter () {
      let req = {
        cityCode: this.studentDetail.cityCode,
        areaId: this.formData2.areaId
      }
      getServiceCenter(req).then((data) => {
        this.serviceCenterList = data
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    getVenue () {
      let req = {
        cityCode: this.studentDetail.cityCode,
        serviceId: this.formData2.serviceCenterId
      }
      getVenue(req).then((data) => {
        this.venueList = data
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },

    //   0 1 2
    getTeacherList (query) {
      query = query.trim()
      if (query !== '' && query !== this.oldSearchName) {
        let req = {
          cityCode: this.studentDetail.cityCode,
          businessType: this.formData2.classType,
          sysName: query,
          teacherType: 0,
          pageNo: 1,
          pageSize: 20
        }
        this.searchLoading = true
        getTeacherByNameFuzzy(req).then((data) => {
          this.teacherList = data.records
          this.searchLoading = false
        }).catch((err) => {
          this.$Message.warning({ content: ':' + err.message, duration: 5 })
          this.searchLoading = false
        })
      }
    },
    getTeacherList2 (query) {
      query = query.trim()
      if (query !== '' && query !== this.oldSearchName) {
        let req = {
          cityCode: this.studentDetail.cityCode,
          businessType: this.formData2.classType,
          sysName: query,
          teacherType: 1,
          pageNo: 1,
          pageSize: 20
        }
        this.searchLoading = true
        getTeacherByNameFuzzy(req).then((data) => {
          this.tutorTeacherList = data.records
          this.searchLoading = false
        }).catch((err) => {
          this.$Message.warning({ content: ':' + err.message, duration: 5 })
          this.searchLoading = false
        })
      }
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },

    // 
    gradeTypeChange (val) {
      this.formData2.gradeId = ''
      this.formData2.levelId = ''
      this.gradeList = []
      this.levelList = []
      if (!val) {
        return
      }
      this.getGradeList()
    },
    // 
    gradeChange (val) {
      this.formData2.levelId = ''
      this.levelList = []
      if (!val) {
        return
      }
      this.getLevelList()
    },
    // 
    getGradeList (val) {
      let req = {
        cityCode: this.studentDetail.cityCode,
        queryType: 'g',
        year: this.oldClassInfo.year,
        termId: this.oldClassInfo.termId,
        gtIds: this.formData2.gradeTypeId
      }
      getLinkage(req).then((data) => {
        this.gradeList = data
        if (val) {
          this.formData2.gradeId = val
        }
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    getLevelList () {
      let req = {
        cityCode: this.studentDetail.cityCode,
        queryType: 'c',
        year: this.oldClassInfo.year,
        termId: this.oldClassInfo.termId,
        gtIds: this.formData2.gradeTypeId,
        gradeIds: this.formData2.gradeId,
        classType: this.formData2.classType
      }
      getLinkage(req).then((data) => {
        this.levelList = data
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },

    // 
    getBaseData () {
      // t gt g s ,cd c 
      // 
      let req1 = {
        cityCode: this.studentDetail.cityCode,
        queryType: 'gt',
        years: this.oldClassInfo.year,
        termIds: this.oldClassInfo.termId
      }
      getLinkage(req1).then((data) => {
        this.gradeTypeList = data
        this.formData2.gradeTypeId = this.oldClassInfo.gradeTypeId
        this.getGradeList(this.oldClassInfo.gradeId)
        this.getLevelList()
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
      // 
      let req4 = {
        cityCode: this.studentDetail.cityCode
      }
      getArea(req4).then((data) => {
        this.areaList = data
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
      // 
      // getClassTimeTypeList(req4).then((data) => {
      //   this.classTimeTypeList = data
      // }).catch((err) => {
      //   this.$Message.warning({content:':' + err.message,duration:5})
      // })
    },

    // 
    getChangeClassType () {
      getChangeClassType({
        cityCode: this.studentDetail.cityCode
      }).then((res) => {
        this.changeClassTypeList = res
        if (this.changeClassTypeList) {
          this.formData1.reasonTypeId = res[0].chanageClassTypeId
        }
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    beginNumChange (val) {
      if (val && val > this.oldClassInfo.classCount) {
        this.$Message.warning({ content: '', duration: 5 })
        this.$nextTick(function () {
          this.formData1.beginNum = this.oldClassInfo.classCount
        })
      } else {
        this.formData1.beginNum = val
      }
    },
    // 
    queryTargetClass () {
      if (this.formData2.claStartCount != null && this.formData2.claEndCount != null) {
        if (this.formData2.claStartCount > this.formData2.claEndCount) {
          this.$Message.warning('')
          return
        }
      }
      if (this.formData2.minRemainNum != null && this.formData2.maxRemainNum != null) {
        if (this.formData2.minRemainNum > this.formData2.maxRemainNum) {
          this.$Message.warning('')
          return
        }
      }
      this.formData2.year = this.oldClassInfo.year
      this.formData2.termId = this.oldClassInfo.termId
      this.formData2.subjectIds = this.oldClassInfo.subjectIds
      this.formData2.cityCode = this.studentDetail.cityCode
      this.formData2.pageNo = this.pageNum
      this.formData2.pageSize = this.pageSize
      if (!this.formData2.gradeTypeId) {
        this.$Message.warning({ content: '', duration: 5 })
        return
      }
      getClassInfo(this.formData2).then((res) => {
        this.tableData = res.records
        this.pageTotal = res.totalCount
        this.tableData.forEach((e) => {
          if (e.startDate) {
            e.startDate = e.startDate.substring(0, 10)
          }
          if (e.endDate) {
            e.endDate = e.endDate.substring(0, 10)
          }
        })
        this.$Message.success('')
      }).catch((err) => {
        this.tableData = []
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    changeConfirm (row) {
      this.formData1.targetClassId = row.id
      this.targetClassName = row.name
      this.modalValue2 = false
    },
    getClassType (type) {
      return this.classTypeMap[type]
    },
    pageChange (val) {
      this.pageNum = val
      this.queryTargetClass()
    },
    pageSizeChange (size) {
      this.pageSize = size
      this.pageNum = 1
      this.queryTargetClass()
    },
    search () {
      this.pageNum = 1
      this.queryTargetClass()
    },
    setEnterEvent (e) {
      let key = window.event.keyCode
      if (key === 13) {
        this.search()
      }
    }
  },
  // 
  watch: {
    modalValue2: function (val) {
      if (val) {
        this.getBaseData()
        // enter
        document.addEventListener('keydown', this.setEnterEvent)
      } else {
        document.removeEventListener('keydown', this.setEnterEvent)
        this.tableData = []
        this.pageTotal = 0
        this.pageNum = 1
        this.pageSize = 10
        // this.gradeList = []
      }
    }
  },
  computed: {
    modalValue: {
      get: function () {
        if (this.value === true) {
          this.formData1.beginNum = this.oldClassInfo.passedCount + 1
          this.getChangeClassType()
        }
        return this.value
      },
      set: function (val) {
        if (val === false) {
          // modal
          this.formData1.registId = ''
          this.formData1.studentId = ''
          this.formData1.targetClassId = ''
          this.formData1.sourceClassId = ''
          this.formData1.cityCode = ''
          this.formData1.description = ''
          this.formData1.reasonTypeId = ''
          this.formData1.isSpecial = 0
          this.targetClassName = ''
          this.$emit('close')
        }
      }
    }
  },
  mounted () {
    this.getClassTypeHandler()
  }
}
</script>
