  .stuRight{
    margin-left: 10px;
  }
  .class_top {
    width: 100%;
    background: #fcfcfc;
  }

  .class_nav {
    position: relative;
    width: 100%;
    background: #fff;
    margin-top: 15px;
    font-size: 14px;
    padding: 10px;
  }
</style>
