import $ from '../utils/api.request'

/**
 * 
 * @param params
 * @returns {*}
 */
export function queryPhones (params) {
  return $.splatAxios.request({
    url: 'smsCode/queryPhones',
    method: 'get',
    params
  })
}

/**
 * 
 * @param params
 * @returns {*}
 */
export function sendMessage (params) {
  return $.splatAxios.request({
    url: 'smsCode/sendMessage',
    method: 'get',
    params
  })
}
